@mixin safari-overflow {
  overflow: hidden;
  mask-image: -webkit-radial-gradient(white, black);
}

@mixin hide-scrollbar {
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
    height: 0;
    width: 0;
  }
}

@mixin fix-image-quality {
  image-rendering: -webkit-optimize-contrast;
  transform: translateZ(0);
}

@mixin one-line-limit {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
}

@mixin background-image(
  $image,
  $size: contain,
  $position: center center,
  $repeat: no-repeat
) {
  background-image: url($image);
  background-size: $size;
  background-position: $position;
  background-repeat: $repeat;
}

@mixin multi-line-limit($lines: 2, $line-height: false) {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $lines;
  @if $line-height {
    max-height: $line-height * $lines * 1px;
  }
}

@mixin image-resize(
  $object-fit: cover,
  $object-position: center,
  $full-size: true
) {
  object-fit: $object-fit;
  object-position: $object-position;
  display: block;
  @if $full-size {
    width: 100%;
    height: 100%;
  }
}

@mixin pseudo-element(
  $left: 0,
  $top: 0,
  $width: 100%,
  $height: 100%,
  $right: unset,
  $bottom: unset
) {
  content: "";
  position: absolute;
  left: $left;
  right: $right;
  top: $top;
  bottom: $bottom;
  width: $width;
  height: $height;
}

@mixin absolute-element(
  $left: 0,
  $top: 0,
  $width: 100%,
  $height: 100%,
  $right: unset,
  $bottom: unset
) {
  position: absolute;
  left: $left;
  right: $right;
  top: $top;
  bottom: $bottom;
  width: $width;
  height: $height;
}

@mixin flex-element($justify: center, $align: center, $direction: row) {
  display: flex;
  flex-direction: $direction;
  align-items: $align;
  justify-content: $justify;
}

@mixin reset-input() {
  resize: none;
  appearance: none;
  backface-visibility: hidden;
  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    appearance: none;
  }
  &::-webkit-inner-spin-button,
  &::-webkit-calendar-picker-indicator {
    display: none;
    appearance: none;
  }
  &::-ms-expand {
    display: none;
  }
}

@mixin file-input-hover-fix() {
  &::-webkit-file-upload-button {
    cursor: pointer;
  }
}

@mixin svg-color($color: #5e5e5e) {
  .fill {
    fill: $color;
  }
  .stroke {
    stroke: $color;
  }
}

@mixin three-dot($width: 100%, $display: block) {
  width: $width;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: $display;
}

@mixin text-clamp($lines: 2, $line-height: false) {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $lines;
  @if $line-height {
    max-height: $line-height * $lines * 1px;
  }
}