.pagination-field {
  .pagination {
    ul {
      display: flex;
      justify-content: center;
      li {
        margin-right: 20px;
        &:last-child {
          margin-right: 0;
        }
        span,
        a {
          color: #d6d6d6;
          font-size: 19px;
          line-height: 1.5;
          border: 1px solid rgba($primary-color, 0.2);
          width: 42px;
          height: 42px;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: $pagination-radius;
          transition: 0.45s;
          &.active,
          &:hover {
            color: #fff;
            border-color: $primary-color;
            background: $primary-color;
            box-shadow: $box-shadow-primary !important;
            svg {
              @include svg-color(#fff);
            }
          }
          svg {
            @include svg-color(#9d9d9d);
          }
        }
        span {
          cursor: not-allowed;
        }
      }
    }
  }
}
