.button {
  display: flex;
  align-items: center;
  justify-content: center;
  outline: unset;
  cursor: pointer;
  transition: 0.3s ease all;
  background-color: transparent;
  position: relative;
  backface-visibility: hidden;
  border: $button-border;
  height: $button-height;
  padding: $button-padding;
  border-radius: $button-border-radius;
}
