.carousel__button.is-close {
  svg {
    transition: 0.3s ease all;
  }
  &:hover {
    svg {
      transform: rotate(-90deg);
    }
  }
}

.popup {
  display: none;
  max-width: $popup-max-width;
  border-radius: 20px;
  background: #111111;
  @media only screen and (max-width: 991px) {
    .popup {
      max-width: 100%;
    }
  }
}

#popup-gdpr {
  .editor {
    max-height: 500px;
  }
}

.kvkk-popup {
  width: 100%;
  height: 100%;
  max-width: 550px;
  max-height: 90%;
  padding: 30px 0;
  margin: 0;
  background-color: white;
  .wrapper {
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 0 30px;
    .inner {
      width: 100%;
      height: 100%;
      text-align: justify;
    }
  }
}

.popup-products {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 101;
  height: 100%;
  background: #111111;
  border-radius: 50px 0 0 50px;
  overflow: hidden;
  transition: 0.65s ease;
  &:hover {
    .inner {
      .content {
        .background-image {
          .img {
            opacity: 0.15;
          }
        }
      }
    }
  }
  &.popup-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform: translateX(100%);
  }
  .inner {
    height: 100%;
    padding: 50px 50px;
    position: relative;
    .content {
      max-width: 575px;
      min-width: 575px;
      .background-image {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
        .img {
          height: 100%;
          opacity: 0.1;
          transition: 0.45s;
        }
      }
      .top-field {
        position: relative;
        z-index: 5;
        .logo-field {
          margin-bottom: 50px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          .close-button {
            display: inline-flex;
            cursor: pointer;
            transition: 0.45s;
            .icon {
              display: flex;
              align-items: center;
              @include svg-color(#fff);
            }
            &:hover {
              transform: rotate(90deg);
              .icon {
                @include svg-color($primary-color);
              }
            }
          }
        }
        .editor {
        }
        .img-field {
          border-radius: 25px;
          margin-bottom: 30px;
        }
      }
      .form-field {position: relative;
        z-index: 5;
        .item {
          display: grid;
          grid-template-columns: minmax(0, 3fr) minmax(0, 6fr);
          border-bottom: 1px solid rgba(255, 255, 255, 0.3);
          transition: 0.45s ease all;
          &:before {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            width: 0;
            height: 1px;
            background: #fff;
            transition: 0.45s ease all;
          }
          &:hover {
            &:before {
              width: 100%;
            }
            label {
              color: rgba(#e3e3e3, 1);
            }
          }
          input:not([type="radio"]),
          input:not([type="checkbox"]),
          textarea,
          select {
            box-shadow: unset;
            border-radius: 0;
            transition: 0.45s ease all;
            &::placeholder {
              color: rgba(#e3e3e3, 0.25);
            }
            &:hover,
            &:focus {
              &::placeholder {
                color: rgba(#e3e3e3, 1);
              }
            }
          }
          &.custom-select {
            .select {
              grid-column: span 2;
              padding-left: 0;
            }
          }
          &.custom-checkbox-input {
            border-bottom: unset;
            align-self: center;
            &:before {
              display: none;
            }
            .gdpr-field {
              grid-column: span 2;
              label {
                font-size: 14px;
              }
            }
          }
          &.button-field {
            border-bottom: unset;
            display: flex;
            justify-content: center;
            align-items: center;
            &:before {
              display: none;
            }
          }
          textarea {
            height: $input-height;
          }
          label {
            color: rgba(#e3e3e3, 0.75);
            font-size: 18px;
            line-height: 1.5;
            align-self: center;
            transition: 0.45s;
          }
        }
      }
    }
  }
}
