.project-filter-field {
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: minmax(0, auto) minmax(0, 1fr);
      grid-gap: 120px;
      .title-field {
        position: relative;
        .title {
          font-size: 36px;
          color: #e3e3e3;
          line-height: 1.25;
          font-weight: bold;
          &::before {
            content: "";
            position: absolute;
            right: -60px;
            top: 50%;
            transform: translateY(-50%);
            width: 1px;
            height: 100%;
            pointer-events: none;
            background: rgba(#ffffff, 0.25);
          }
        }
      }
      .filter-list {
        ul {
          display: flex;
          justify-content: space-between;
          li {
            position: relative;
            padding-bottom: 15px;
            cursor: pointer;
            &:before {
              content: "";
              position: absolute;
              left: 0;
              bottom: 0;
              width: 0;
              height: 2px;
              background: $primary-color;
              transition: 0.45s ease all;
            }
            .text {
              font-size: 18px;
              font-weight: 500;
              color: #fff;
            }
            &:hover {
              &:before {
                width: 50%;
              }
            }
            &.active {
              &:before {
                width: 100%;
              }
              .text {
                color: $primary-color;
              }
            }
          }
        }
      }
    }
  }
}

.project-list-field {
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: repeat(1, minmax(0, 1fr));
      grid-gap: 36px;
    }
  }
}

.project-box {
  display: grid;
  grid-template-columns: minmax(0, 7fr) minmax(0, 5fr);
  grid-gap: 36px;
  position: relative;
  border-radius: 30px;
  margin-bottom: 30px;
  width: 100%;
  overflow: hidden;
  &:hover {
    &:after {
      opacity: 1;
      visibility: visible;
    }
    .info-field {
      .info-content {
        height: 75%;
        .image {
          background: $primary-color;
        }
        .text-field {
          background: $primary-color;
          .current {
            margin-bottom: 20px;
            transform: translateY(-80px);
          }
          .button {
            opacity: 1;
            visibility: visible;
            transform: translateY(-80px);
          }
        }
      }
    }
  }
  &:before {
    @include pseudo-element;
    background: linear-gradient(
      90deg,
      rgba(#141414, 0) 0%,
      rgba(#141414, 1) 40%
    );
    z-index: 1;
    pointer-events: none;
  }
  &:after {
    @include pseudo-element;
    background: linear-gradient(
      90deg,
      rgba($primary-color, 0) 0%,
      rgba($primary-color, 1) 40%
    );
    z-index: 2;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: 0.45s ease all;
  }
  .image-field {
    .img {
      height: 450px;
      border-radius: 30px 0 0 30px;
    }
  }
  .info-field {
    position: relative;
    z-index: 10;
    margin-right: 100px;
    .info-content {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;
      height: 60%;
      transition: 0.45s;
      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 30px;
        border: 1px solid #e3e3e3;
        pointer-events: none;
      }
    }
    .image {
      background: #141414;
      position: absolute;
      z-index: 6;
      left: 30px;
      top: 30px;
      width: 110%;
      transition: 0.45s ease all;
      .img {
        height: 150px;
      }
    }
    .text-field {
      position: absolute;
      z-index: 8;
      bottom: 0;
      width: 75%;
      left: 0;
      right: 0;
      margin: auto;
      transform: translateY(50%);
      background: #141414;
      padding: 0 30px;
      transition: 0.45s ease all;
      .current {
        transition: 0.45s;
        .category {
          font-size: 20px;
          color: #ffffff;
          line-height: 1.5;
          margin-bottom: 5px;
        }
        .name {
          font-size: 22px;
          color: #ffffff;
          line-height: 1.25;
          font-weight: bold;
          @include text-clamp(2);
          transition: 0.45s;
          &:hover {
            color: darken(#fff, 20%);
          }
        }
      }
      .button {
        opacity: 0;
        visibility: hidden;
        transform: translateY(0);
        position: absolute;
        transition: 0.45s;
      }
    }
  }
}

.homepage-project-box {
  .image-field {
    max-width: 900px;
    width: 100%;
    margin: auto;
    .img {
      height: 300px;
      img {
      }
    }
  }
  .text-field {
    max-width: 900px;
    width: 100%;
    margin: auto;
    .category {
      font-size: 18px;
      color: #fff;
      line-height: 1.5;
      margin-bottom: 5px;
    }
    .name {
      font-size: 40px;
      color: #fff;
      line-height: 1.25;
      font-weight: 600;
      margin-bottom: 30px;
      display: inline-flex;
      transition: 0.45s;
      &:hover {
        color: $primary-color;
      }
    }
    .description {
      font-size: 18px;
      color: #fff;
      line-height: 2;
      margin-bottom: 50px;
    }
    .button-field {
      display: flex;
      justify-content: center;
      gap: 20px;
    }
  }
}

.threed-field {
  position: relative;
  margin-bottom: 30px;
  height: 600px;
  &:last-child {
    margin-bottom: 0;
  }
  .model-glb {
    width: 100%;
    height: 600px;
  }
  .obj-field {
    position: absolute;
    width: 100%;
    height: 600px;
    border-radius: 30px;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transition: 0.45s;
    &.active {
      opacity: 1;
      visibility: visible;
    }
    .viewer {
      width: 100%;
      height: 100%;
      padding-bottom: 100px;
      model-viewer {
        width: 100%;
        height: 100%;
      }
    }
    .model-images {
      width: 100%;
      position: relative;
      cursor: grab;
      height: 600px;
      .model-image {
        z-index: 2;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
        display: none;
        pointer-events: none;
        user-select: none;
        &:first-child {
          display: block;
        }
      }
    }
  }
  .placeholder-image {
    transition: 0.45s;
    &.hidden {
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }
    .main-image {
      .img {
        position: relative;
        border-radius: 30px;
        height: 600px;
        border-radius: 30px;
        overflow: hidden;
        visibility: visible;
        opacity: 1;
        transition: 0.45s;
        img {
          padding: 20px;
        }
      }
    }
  }
  &.obj-draggable {
    height: $homepage-three-height;
    .obj-field {
      height: $homepage-three-height;
      .full {
        width: 100%;
        height: $homepage-three-height;
      }
    }
  }
}

.similar-project-field {
  overflow: hidden;
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: repeat(1, minmax(0, 1fr));
      grid-gap: 30px;
      .similar-project-carousel {
        overflow: visible;
        width: 100%;
        position: relative;
        padding: 0 1px;
        &:before {
          @include pseudo-element;
          left: -100%;
          height: calc(100% + 100px);
          background: #000;
          z-index: 15;
          pointer-events: none;
        }
        &-prev {
          pointer-events: none;
        }
      }
    }
  }
}

