.project-detail-field {
  height: 100vh;
  .main-content {
    .main-container {
      height: 100%;
      position: relative;
      .tab {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transform: translateY(20px);
        transition: 0.45s;
        &.active {
          opacity: 1;
          visibility: visible;
          pointer-events: all;
          transform: translateY(0);
          transition-delay: 0.45s;
        }
        &--one {
          .wrapper {
            height: 100%;
            .project-carousel {
              height: 100%;
              .swiper-wrapper {
                .swiper-slide {
                  .content {
                    height: 100%;
                    .image-field {
                      height: 100%;
                      .img {
                        height: 100%;
                        video {
                          width: 100%;
                          height: 100%;
                          object-fit: cover;
                        }
                      }
                      .text-container {
                        left: 0;
                        right: 0;
                        margin: auto;
                        position: absolute;
                        bottom: 50px;
                        .text-wrapper {
                          display: grid;
                          grid-template-columns: repeat(2, minmax(0, 1fr));
                          grid-gap: 30px;
                          .left-field {
                            .category {
                              font-size: 20px;
                              color: #fff;
                              line-height: 1.5;
                              margin-bottom: 10px;
                            }
                            .title {
                              font-size: 36px;
                              color: #fff;
                              line-height: 1.25;
                              font-weight: bold;
                            }
                          }
                          .right-field {
                            align-self: flex-end;
                            .button-field {
                              display: flex;
                              justify-content: flex-end;
                              .button-box {
                                margin-right: 30px;
                                padding-right: 30px;
                                position: relative;
                                &:before {
                                  content: "";
                                  position: absolute;
                                  right: 0;
                                  top: 50%;
                                  transform: translateY(-50%);
                                  width: 1px;
                                  height: 100%;
                                  background: rgba(#ffffff, 0.25);
                                  pointer-events: none;
                                }
                                &:last-child {
                                  margin-right: 0;
                                  padding-right: 0;
                                  &:before {
                                    display: none;
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
              .carousel-controller {
                position: absolute;
                right: 0;
                bottom: 50px;
                z-index: 20;
                width: 100%;
                pointer-events: none;
                .button-container {
                  .button-field {
                    display: flex;
                    justify-content: flex-end;
                    .button-box {
                      margin-right: 30px;
                      padding-right: 30px;
                      position: relative;
                      pointer-events: all;
                      &:before {
                        content: "";
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        width: 1px;
                        height: 100%;
                        background: rgba(#ffffff, 0.25);
                        pointer-events: none;
                      }
                      &:last-child {
                        margin-right: 0;
                        padding-right: 0;
                        &:before {
                          display: none;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
        &--two {
          .wrapper {
            height: 100%;
            .image-wrapper {
              pointer-events: none;
              .wave {
                height: calc(($product-wave-height / 2));
                position: absolute;
                width: 100%;
                bottom: 0;
                pointer-events: none;
                &:before {
                  @include pseudo-element;
                  background: linear-gradient(
                    180deg,
                    rgba(#000, 0) 0%,
                    rgba(#000, 1) 80%
                  );
                  z-index: 15;
                }
                .img {
                  position: absolute;
                  bottom: 0;
                  left: 0;
                  height: 100%;
                  width: 100%;
                }
                &--back {
                  z-index: 5;
                }
                &--front {
                  z-index: 15;
                }
              }
              .image-field {
                position: absolute;
                height: $product-wave-height;
                width: 50%;
                z-index: 10;
                left: 0;
                bottom: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                .placeholder-image {
                  .img {
                    position: relative;
                    border-radius: 30px;
                    transition: 0.45s;
                    height: 600px;
                    border-radius: 30px;
                    overflow: hidden;
                    visibility: visible;
                    opacity: 1;
                    img {
                      padding: 20px;
                    }
                    &.hidden {
                      opacity: 0;
                      visibility: hidden;
                    }
                  }
                }
              }
            }
            .text-container {
              left: 0;
              right: 0;
              margin: auto;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              z-index: 15;
              .text-wrapper {
                display: grid;
                grid-template-columns: repeat(2, minmax(0, 1fr));
                grid-gap: 200px;
                .left-field {
                  align-self: flex-start;
                  .category {
                    font-size: 20px;
                    color: #fff;
                    line-height: 1.5;
                    margin-bottom: 10px;
                  }
                  .title {
                    font-size: 36px;
                    color: #fff;
                    line-height: 1.25;
                    font-weight: bold;
                  }
                }
                .right-field {
                }
              }
            }
          }
        }
        &--three {
          background: linear-gradient(
            180deg,
            rgba(#000000, 1) 0%,
            rgba(#195a78, 1) 100%
          );
          .wrapper {
            overflow: hidden;
            display: grid;
            grid-template-columns: repeat(1, minmax(0, 1fr));
            grid-template-rows: repeat(5, minmax(0, 1fr));
            height: 100%;
            padding-top: var(--header-height);
            .carousel-container {
              overflow: visible;
              grid-row: span 3;
              margin-top: 30px;
              .project-media-carousel {
                overflow: visible;
                width: 100%;
                height: 100%;
                position: relative;
                .swiper-wrapper {
                  .swiper-slide {
                    transition: 0.45s;
                    a {
                      height: 100%;
                    }
                    &-prev-prev,
                    &-prev {
                      pointer-events: none;
                      opacity: 0;
                      visibility: hidden;
                    }
                    .image {
                      height: 100%;
                      .img {
                        height: 100%;
                      }
                    }
                  }
                }
              }
            }
            .text-container {
              position: relative;
              z-index: 15;
              width: 100%;
              grid-row: span 2;
              align-self: center;
              .text-wrapper {
                display: grid;
                grid-template-columns: repeat(2, minmax(0, 1fr));
                grid-gap: 200px;
                .left-field {
                  align-self: flex-start;
                  .category {
                    font-size: 20px;
                    color: #fff;
                    line-height: 1.5;
                    margin-bottom: 10px;
                  }
                  .title {
                    font-size: 36px;
                    color: #fff;
                    line-height: 1.25;
                    font-weight: bold;
                  }
                }
                .right-field {
                  align-self: center;
                  justify-self: flex-end;
                  padding-right: 5px;
                  .carousel-controller {
                    .carousel-navigation {
                      display: flex;
                      .project-media-next,
                      .project-media-prev {
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        transition: 0.45s;
                        .icon {
                          display: flex;
                          align-items: center;
                          @include svg-color(#fff);
                          transition: 0.45s;
                        }
                        &:hover {
                          .icon {
                            @include svg-color($primary-color);
                          }
                        }
                      }
                      .project-media-next {
                        &:hover {
                          .icon {
                            transform: translateX(5px);
                          }
                        }
                      }
                      .project-media-prev {
                        margin-right: 30px;
                        .icon {
                          transform: rotate(180deg);
                        }
                        &:hover {
                          .icon {
                            transform: translateX(-5px) rotate(180deg);
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
        &--four {
          background: linear-gradient(
            180deg,
            rgba(#000000, 1) 0%,
            rgba(#195a78, 1) 100%
          );
          .wrapper {
            display: grid;
            grid-template-columns: repeat(1, minmax(0, 1fr));
            position: relative;
            height: 100%;
            .image-content-field {
              position: relative;
              z-index: 30;
              transition: 0.45s;
              .image-content {
                position: absolute;
                width: 100%;
                height: 100%;
                opacity: 0;
                visibility: hidden;
                transition: 0.45s;
                height: calc(100vh - 80px);
                &.active {
                  opacity: 1;
                  visibility: visible;
                }
                &--one {
                  padding-top: var(--header-height);
                  .threed-field {
                    height: calc(
                      100vh - var(--header-height) - 80px - 150px - 50px - 20px
                    );
                    .obj-field {
                      height: 100%;
                      #model {
                        height: 100%;
                        .model-images {
                          height: 100% !important;
                        }
                      }
                    }
                    .placeholder-image {
                      height: 100%;
                      .main-image {
                        height: 100%;
                        position: relative;
                        .img {
                          height: 100%;
                          position: relative;
                          z-index: 5;
                        }
                        .img-bottom {
                          position: absolute;
                          bottom: 0;
                          left: 0;
                          right: 0;
                          width: 50%;
                          height: 50%;
                          margin: auto;
                          z-index: 3;
                        }
                      }
                    }
                  }
                }
                &--two {
                  position: relative;
                  .vessel-placeholder {
                    position: relative;
                    height: 100%;
                    cursor: pointer;
                    .main-image {
                      height: 100%;
                      .img {
                        height: 100%;
                        &:before {
                          @include pseudo-element;
                          background: linear-gradient(
                            180deg,
                            rgba(#000000, 1) 0%,
                            rgba(#000000, 0) 100%
                          );
                        }
                      }
                    }
                    .icon-field {
                      position: absolute;
                      top: 50%;
                      left: 50%;
                      transform: translateY(-50%) translateX(-50%);
                      z-index: 20;
                      width: 200px;
                      height: 200px;
                      border-radius: 100%;
                      display: flex;
                      justify-content: center;
                      align-items: center;
                      background: $primary-color;
                      transition: 0.45s;
                      &:hover {
                        background: darken($primary-color, 10%);
                      }
                      .img {
                        max-width: 100px;
                      }
                    }
                  }
                }
              }
            }
            .text-container {
              left: 0;
              right: 0;
              margin: auto;
              position: absolute;
              bottom: 50px;
              z-index: 50;
              .text-wrapper {
                display: grid;
                grid-template-columns: repeat(2, minmax(0, 1fr));
                grid-gap: 30px;
                .left-field {
                  .category {
                    font-size: 20px;
                    color: #fff;
                    line-height: 1.5;
                    margin-bottom: 10px;
                  }
                  .title {
                    font-size: 36px;
                    color: #fff;
                    line-height: 1.25;
                    font-weight: bold;
                  }
                }
                .right-field {
                  align-self: flex-end;
                  display: grid;
                  grid-template-columns: repeat(2, minmax(0, 1fr));
                  .button-field {
                    grid-column-start: 2;
                    .button-info {
                      display: grid;
                      grid-template-columns: minmax(0, auto) minmax(0, 1fr);
                      margin-bottom: 20px;
                      .left-info {
                        align-self: center;
                        margin-right: 15px;
                        .img {
                          max-width: 100px;
                        }
                      }
                      .right-info {
                        align-self: center;
                        .text {
                          font-size: 16px;
                          color: #fff;
                          line-height: 1.5;
                          font-weight: 600;
                        }
                      }
                    }
                    ul {
                      display: flex;
                      border: 1px solid rgba(#ffffff, 0.25);
                      border-radius: 50px;
                      li {
                        padding: 15px 20px;
                        border-radius: 50px;
                        position: relative;
                        width: 50%;
                        position: relative;
                        text-align: center;
                        cursor: pointer;
                        &:before {
                          content: "";
                          position: absolute;
                          left: 0;
                          top: 0;
                          width: 100%;
                          height: 100%;
                          background: transparent;
                          border-radius: 50px;
                          transition: 0.45s;
                        }
                        .text {
                          font-size: 16px;
                          color: #fff;
                          line-height: 1.5;
                          font-weight: 600;
                          white-space: nowrap;
                          position: relative;
                          z-index: 1;
                        }
                        &.active {
                          &:before {
                            background: $primary-color;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .tab-field {
    background: #000;
    position: relative;
    z-index: 20;
    .container {
      .wrapper {
        .tab-content {
          ul {
            display: grid;
            grid-template-columns: repeat(4, minmax(0, 1fr));
            li {
              text-align: center;
              padding: 25px 0;
              cursor: pointer;
              transition: 0.45s;
              &:hover {
                background: rgba($primary-color, 0.3);
              }
              &.active {
                background: $primary-color;
                .text {
                }
              }
              .text {
                font-size: 20px;
                color: #fff;
                line-height: 1.5;
                font-weight: bold;
              }
            }
          }
        }
      }
    }
  }
}
