.b--icon {
  &--size {
    &-smallest svg {width: 8px !important;height: 8px !important;}
    &-smaller svg {width: 10px !important;height: 10px !important;}
    &-small svg {width: 14px !important;height: 14px !important;}
    &-medium svg {width: 18px !important;height: 18px !important;}
    &-large svg {width: 22px !important;height: 22px !important;}
  }
  &--position {
    &-left svg {margin-right: 10px !important;}
    &-right svg {margin-left: 10px !important;}
  }
  svg {
    max-height: 25px;
    max-width: 25px;
    width: 100%;
    flex-shrink: 0;
    transition: 0.3s ease all;
    position: relative;
    backface-visibility: hidden;
    z-index: 1;
  }
}
