.breadcrumb {
  .wrapper {
    display: grid;
    .image-field {
      position: relative;
      .img {
        height: calc(600px - $page-content-padding);
        position: relative;
        &:before {
          @include pseudo-element;
          background: linear-gradient(
            0deg,
            rgba(#000, 1) 0%,
            rgba(#000, 0) 100%
          );
        }
        img {
        }
        video {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .text-container {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        margin: auto;
        left: 0;
        right: 0;
        z-index: 5;
        .text-field {
          display: grid;
          grid-template-columns: minmax(0, 1fr) minmax(0, auto);
          grid-gap: 24px;
          .navigation {
            align-self: center;
            .title {
              font-size: 40px;
              color: #e3e3e3;
              line-height: 1.25;
              font-weight: bold;
            }
            ul {
              margin-top: 5px;
              display: flex;
              flex-wrap: wrap;
              li {
                margin-right: 10px;
                padding-right: 20px;
                position: relative;
                &:before {
                  content: ">";
                  position: absolute;
                  right: 0;
                  top: 50%;
                  transform: translateY(-50%);
                  color: #e3e3e3;
                  font-size: 20px;
                  line-height: 1.5;
                }
                &:last-child {
                  margin-right: 0;
                  padding-right: 0;
                  &:before {
                    display: none;
                  }
                }
                a {
                  &.deactive {
                    cursor: default;
                    &:hover {
                      .text {
                        color: #e3e3e3;
                      }
                    }
                  }
                  .text {
                    font-size: 20px;
                    color: #e3e3e3;
                    line-height: 1.5;
                    font-weight: 300;
                  }
                  &:hover {
                    .text {
                      color: darken(#e3e3e3, 10%);
                    }
                  }
                }
              }
            }
          }
          .scroll {
            .content {
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              cursor: pointer;
              &:hover {
                .icon {
                  background: #e3e3e3;
                  transform: translateY(17px);
                  @include svg-color(#000);
                }
                .text {
                  transform: translateY(-34px);
                  opacity: 0;
                  visibility: hidden;
                }
              }
              .icon {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 60px;
                height: 60px;
                border-radius: 100%;
                border: 1px solid #e3e3e3;
                @include svg-color(#e3e3e3);
                transition: 0.45s;
                svg {
                  width: 18px;
                }
              }
              .text {
                font-size: 16px;
                color: #e3e3e3;
                line-height: 1.5;
                font-weight: 300;
                margin-top: 10px;
                transition: 0.45s;
              }
            }
          }
        }
      }
    }
  }
}
