.form {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  grid-auto-rows: auto;
  grid-gap: 20px 30px;
  .item {
    position: relative;
    input:not([type="radio"]),
    input:not([type="checkbox"]),
    textarea,
    select {
      @include reset-input;
      height: $input-height;
      border: $input-border;
      padding: $input-padding;
      border-radius: $input-border-radius;
      color: $input-color;
      background-color: $input-background;
      box-shadow: inset 0 0 0 1px $border-color;
      width: 100%;
      margin: 0;
      font-size: 1rem;
      transition: 0.3s ease all;
      &.error {
        border-color: red;
      }
      &::placeholder {
        color: rgba(#fff, 1);
        transition: .45s;
      }
      &:hover {
        box-shadow: inset 0 0 0 1px lighten($primary-color, 40%);
      }
      &:focus {
        box-shadow: inset 0 0 0 1px $primary-color;
      }
    }
    textarea {
      padding: $textarea-padding;
      height: $textarea-height;
      border-radius: $textarea-border-radius;
    }
    &.size {
      &--one-quarter {grid-column: span 1;}
      &--half {grid-column: span 2;}
      &--three-quarters {grid-column: span 3;}
      &--full {grid-column: span 4;}
    }
    &.justify {
      &--start {justify-self: start;}
      &--center {justify-self: center;}
      &--end {justify-self: end;}
    }
    &.align {
      &--start {align-self: start;}
      &--center {align-self: center;}
      &--end {align-self: end;}
    }
  }
  &--three {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    .item {
      &.size {
        &--one {grid-column: span 1;}
        &--two {grid-column: span 2;}
        &--full {grid-column: span 3;}
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .form {
    grid-template-columns: minmax(0, 1fr);
  }
  .form .item.size--half,
  .form .item.size--three-quarters,
  .form .item.size--one-quarter,
  .form .item.size--full {
    grid-column: unset;
  }
  .form .item.custom-file .wrapper,
  .form .item .custom-file .wrapper {
    flex-direction: column;
  }
  .form .item.custom-file .wrapper .file-name,
  .form .item .custom-file .wrapper .file-name {
    border-radius: 50px;
  }
  .form .item.custom-file .wrapper .file-button,
  .form .item .custom-file .wrapper .file-button {
    margin-top: 20px;
    width: 100%;
  }
  .form .item.custom-file .wrapper .file-button .file,
  .form .item .custom-file .wrapper .file-button .file {
    width: 100%;
    border-radius: 50px;
  }
}
