.blog-field {
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      grid-gap: 36px;
    }
  }
}

.blog-box-field {
  .blog-box {
    &:nth-child(odd) {
      transform: translateY(30px);
    }
    &:nth-child(even) {
      transform: translateY(-30px);
    }
  }
}

.blog-box {
  &:hover {
    .image-field {
      &:after {
        opacity: 1;
        visibility: visible;
      }
      .img {
        img {
          transform: scale(1.1);
        }
      }
    }
  }
  .image-field {
    position: relative;
    border-radius: $image-radius;
    overflow: hidden;
    &:before {
      @include pseudo-element;
      background: linear-gradient(
        180deg,
        rgba(#192934, 0) 0%,
        rgba(#192934, 1) 100%
      );
      z-index: 1;
      pointer-events: none;
    }
    &:after {
      @include pseudo-element;
      background: linear-gradient(
        180deg,
        rgba(#192934, 0.5) 0%,
        rgba(#192934, 0.5) 100%
      );
      z-index: 2;
      opacity: 0;
      visibility: hidden;
      transition: 0.45s;
      pointer-events: none;
    }
    .img {
      height: 350px;
      img {
        transition: 0.45s;
      }
    }
    .text-field {
      position: absolute;
      bottom: 30px;
      left: 30px;
      width: calc(100% - 60px);
      z-index: 5;
    }
  }
  &--single {
    &:hover {
      .image-field {
        .text-field {
          .left-field {
            .view,
            .date {
              opacity: 1;
              visibility: visible;
              transform: translateY(0);
              height: 24px;
            }
            .name {
              margin-bottom: 15px;
            }
          }
          .right-field {
            opacity: 1;
            visibility: visible;
            transform: translateY(0) rotate(180deg);
          }
        }
      }
    }
    .image-field {
      .text-field {
        display: grid;
        grid-template-columns: minmax(0, 1fr) minmax(0, auto);
        grid-gap: 36px;
        .left-field {
          align-self: flex-end;
          .date {
            color: #e3e3e3;
            font-size: 16px;
            font-weight: 600;
            line-height: 1.5;
            height: 0;
            opacity: 0;
            visibility: hidden;
            transition: 0.45s;
          }
          .name {
            color: #e3e3e3;
            font-size: 30px;
            font-weight: 600;
            line-height: 1.25;
            margin-bottom: 15px;
            @include text-clamp(2);
            transition: 0.45s;
            &:hover {
              color: $primary-color;
            }
          }
          .view {
            display: flex;
            height: 0;
            opacity: 0;
            visibility: hidden;
            transition: 0.45s;
            .icon {
              margin-right: 10px;
              @include svg-color(#e3e3e3);
            }
            .text {
              color: #e3e3e3;
              font-size: 16px;
              font-weight: 600;
              line-height: 1.5;
            }
          }
        }
        .right-field {
          writing-mode: vertical-rl;
          text-orientation: mixed;
          transform: translateY(20px) rotate(180deg);
          opacity: 0;
          visibility: hidden;
          transition: 0.45s;
          .button {
            height: auto;
            padding: 30px 10px;
            .text {
              white-space: nowrap;
            }
          }
        }
      }
    }
  }
  &--featured {
    grid-column: span 2;
    .image-field {
      .img {
        height: 500px;
      }
      .text-field {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-gap: 36px;
        .left-field {
          align-self: flex-end;
          .date {
            color: #e3e3e3;
            font-size: 18px;
            font-weight: 600;
            line-height: 1.5;
          }
          .name {
            color: #e3e3e3;
            font-size: 30px;
            font-weight: 600;
            line-height: 1.25;
            margin-bottom: 15px;
            @include text-clamp(2);
            transition: 0.45s;
            &:hover {
              color: $primary-color;
            }
          }
          .view {
            display: flex;
            .icon {
              margin-right: 10px;
              @include svg-color(#e3e3e3);
            }
            .text {
              color: #e3e3e3;
              font-size: 16px;
              font-weight: 600;
              line-height: 1.5;
            }
          }
        }
        .right-field {
          align-self: flex-end;
          .description {
            font-size: 18px;
            color: #ffffff;
            line-height: 2;
            @include text-clamp(2);
            margin-bottom: 30px;
          }
        }
      }
    }
  }
}

.media-field {
  overflow: hidden;
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: minmax(0, 1fr);
      grid-gap: 30px;
      .media-carousel {
        overflow: visible;
        width: 100%;
        position: relative;
        &:before {
          @include pseudo-element;
          left: -100%;
          background: #000;
          z-index: 13;
          pointer-events: none;
        }
        .swiper-wrapper {
          .swiper-slide {
          }
        }
        .total-count-field {
          font-size: 18px;
          color: #fff;
          line-height: 1.5;
          font-weight: 500;
          display: flex;
          .current {
          }
          .seperator {
            display: flex;
            position: relative;
            width: 20px;
            margin: 0 10px;
            &:before {
              content: "";
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              width: 20px;
              height: 1px;
              background: #fff;
            }
          }
          .totla {
          }
        }
      }
    }
  }
}

.other-news-field {
  overflow: hidden;
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: minmax(0, 1fr);
      grid-gap: 30px;
      .news-carousel {
        width: 100%;
        .swiper-wrapper {
          .swiper-slide {
            .blog-box--single .image-field .text-field {
              grid-template-columns: minmax(0, 1fr);
            }
            .blog-box--single .image-field .text-field .right-field {
              writing-mode: unset;
              text-orientation: unset;
              transform: translateY(20px) rotate(0);
            }
            .blog-box--single .image-field .text-field .right-field .button {
              padding: 0 30px;
              height: $button-height;
            }
            .blog-box--single:hover .image-field .text-field .right-field {
              transform: translateY(0) rotate(0);
            }
          }
        }
      }
    }
  }
}

.blog-content-field {
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      grid-gap: 150px;
      .information-field {
        .text-field {
          display: grid;
          grid-template-columns: minmax(0, 1fr) minmax(0, auto);
          grid-gap: 36px;
          margin-bottom: 30px;
          padding-bottom: 30px;
          position: relative;
          &:before {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 1px;
            background: rgba(#fff, 0.25);
          }
          .title {
            font-size: 36px;
            color: $primary-color;
            line-height: 1.25;
            font-weight: bold;
            grid-column: span 2;
          }
          .left-field {
            .date {
              color: #e3e3e3;
              font-size: 30px;
              font-weight: 600;
              line-height: 1.5;
              margin-bottom: 20px;
            }
            .view {
              display: flex;
              transition: 0.45s;
              .icon {
                margin-right: 10px;
                @include svg-color(#e3e3e3);
              }
              .text {
                color: #e3e3e3;
                font-size: 16px;
                font-weight: 600;
                line-height: 1.5;
              }
            }
          }
          .right-field {
            align-self: flex-end;
            .scroll-animation {
              border: 1px solid $primary-color;
              border-radius: 100px;
              padding: 15px 25px;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              cursor: pointer;
              transition: 0.45s;
              &:hover {
                background: $primary-color;
                .icon {
                  @include svg-color(#fff);
                }
                .text {
                  color: #fff;
                }
              }
              .icon {
                display: flex;
                align-items: center;
                @include svg-color(#e3e3e3);
              }
              .text {
                font-size: 16px;
                color: #e3e3e3;
                line-height: 1.5;
              }
            }
          }
        }
        .controller-field {
          .blog-controller {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            li {
              margin-right: 20px;
              position: relative;
              cursor: pointer;
              display: flex;
              align-items: center;
              transition: 0.45s;
              &:last-child {
                margin-right: 0;
              }
              .icon {
                display: flex;
                align-items: center;
                svg {
                  @include svg-color(rgba(#fff, 0.75));
                }
              }
              &:hover {
                .icon {
                  @include svg-color(#fff);
                }
              }
              &.size {
                .icon {
                  svg {
                    width: 34px;
                  }
                }
              }
              &.link {
                .icon {
                  svg {
                    width: 17px;
                  }
                }
                .text {
                  font-size: 0.625rem;
                  color: #fff;
                  display: block;
                  background-color: rgba($primary-color, 1);
                  position: absolute;
                  bottom: calc(100% + 15px);
                  left: 60%;
                  -webkit-transform: translateX(-50%);
                  transform: translateX(-50%);
                  white-space: nowrap;
                  padding: 10px;
                  border-radius: 10px;
                  opacity: 0;
                  font-weight: 500;
                  z-index: 1;
                  visibility: hidden;
                }
                &.copied {
                  .text {
                    animation: copied 1s forwards;
                  }
                }
              }
              &.print {
                .icon {
                  svg {
                    width: 17px;
                  }
                }
                a {
                  color: rgba(#fff, 1);
                  transition: 0.45s;
                  &:hover {
                    color: #fff;
                  }
                }
              }
            }
          }
          .share-field {
            position: relative;
            display: flex;
            justify-content: space-between;
            .social-media-ul {
              flex-direction: row;
              .sm-li {
                margin-bottom: 0;
                margin-right: 20px;
                &:last-child {
                  margin-right: 0;
                }
                .sm-link {
                  .icon {
                    @include svg-color(rgba(#fff, 1));
                  }
                  &:hover {
                    .icon {
                      @include svg-color(#fff);
                    }
                  }
                }
              }
            }
          }
        }
      }
      .content-field {
        .main-image {
          margin-bottom: 30px;
          .img {
            height: 500px;
          }
        }
      }
    }
  }
}
