.penta-logo {
    display: flex;
    align-items: center;
    width: fit-content;
    min-width: 125px;
    line-height: 1;
    .penta-logo-brand {
        display: flex;
        align-items: center;
        .penta-logo-img {
            width: 33px;
            height: 30px;
            position: relative;
            .penta-logo-p {
                display: block;
                position: absolute;
                left: 0;
                top: 0;
            }
            .penta-logo-y {
                display: block;
                position: absolute;
                left: 8px;
                top: 15px;
                animation: penta-animation 2s linear infinite;
                transform-style: preserve-3d;
            }
        }
    }
    .penta-logo-text {
        font-weight: 500;
        max-width: 82px;
        font-family: 'Segoe UI', Tahoma, Verdana, sans-serif;
        text-transform: uppercase;
        letter-spacing: 2.5px;
        margin-right: 5px;
        transform: scale(0.9) translateZ(0) translate3d(0,0,0) perspective(1px);
        text-align: right;
        transition: opacity .3s ease, max-width .3s ease;
        display: flex;
    }
    .penta-logo-brand {
        strong {
            font-weight: 500;
            overflow: hidden;
            max-width: 0;
            display: block;
            font-family: 'Segoe UI', Tahoma, Verdana, sans-serif;
            text-transform: uppercase;
            letter-spacing: 2.5px;
            margin-left: 5px;
            transform: scale(0.9) translateZ(0) translate3d(0,0,0) perspective(1px);
            transition: max-width .3s ease;
        }
    }
    &.dark {
        .penta-logo-text {
            color: white;
        }
        .penta-logo-brand {
            strong {
                color: white;
            }
        }
    }
    &.light {
        .penta-logo-text {
            color: black;
        }
        .penta-logo-brand {
            strong {
                color: black;
            }
        }
    }
    &:hover {
        .penta-logo-brand {
            strong {
                max-width: 75px!important;
            }
        }
        .penta-logo-text {
            opacity: 0!important;
            max-width: 0!important;
        }
    }
}

@keyframes penta-animation {
    from { transform: scale(0.85) rotateY(0deg) translateZ(0) translate3d(0,0,0) perspective(1px); }
    to {transform: scale(0.85) rotateY(-360deg) translateZ(0) translate3d(0,0,0) perspective(1px); }
}