.is-safari {
  .button {
    mask-image: -webkit-radial-gradient(white, black);
    -webkit-mask-image: -webkit-radial-gradient(white, black);
    span {
      transform: translateZ(0) translate3d(0, 0, 0);
    }
  }
  .image-field,
  .img {
    mask-image: -webkit-radial-gradient(white, black);
    -webkit-mask-image: -webkit-radial-gradient(white, black);
  }
  .homepage-testimonial-field .container .wrapper .image-field .image--two {transform: translateY(-50%) !important;top: 50%;}
  .about-company-field .container .wrapper .image-field .image--two {bottom: 0;top: unset;transform: unset !important;}
  .about-company-field .container .wrapper .image-field .image--one {top: 0;}
}
