.logo {
  @include flex-element;
  height: 100%;
  width: 100%;
  will-change: transform, opacity;
  svg {width: 100%;overflow: visible;}
  img {@include image-resize(contain);image-rendering: optimizeQuality;}
  &.color {
    svg {
      .letter {fill: #fff;}
    }
  }
  &.white {
    svg {
      .letter {fill: white;}
    }
  }
}
.footer-logo-field {
  .logo {
    justify-content: flex-start;
    height: auto;
    img {
      width: max-content;
      height: auto;
    }
  }
}