.custom-acceptance-input {
  display: flex;
  align-items: center;
  .box {
    position: relative;
    cursor: pointer;
    flex-shrink: 0;
    margin-right: 15px;
    input {
      @include absolute-element;
      z-index: 1;
      cursor: pointer;
      padding: 0;
      margin: 0;
      &.error {
        & + .checkbox {
          box-shadow: inset 0 0 0 1px red;
        }
      }
      &:hover {
        & + .checkbox {
          box-shadow: inset 0 0 0 1px lighten(#fff, 40%);
        }
      }
      &:checked {
        & + .checkbox {
          box-shadow: inset 0 0 0 1px $primary-color;
          &::after {
            opacity: 1;
            transform: translateX(-50%) translateY(-50%) scale(1);
          }
        }
      }
    }
    .checkbox {
      border-radius: 0;
      background-color: transparent;
      backface-visibility: hidden;
      cursor: pointer;
      position: relative;
      transition: 0.3s ease all;
      width: 24px;
      height: 24px;
      box-shadow: inset 0 0 0 1px $border-color;
      transition: 0.3s ease all;
      &::after {
        content: "";
        border-radius: 0;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%) scale(0.8);
        width: 10px;
        height: 10px;
        background-color: $primary-color;
        opacity: 0;
        transition: 0.3s ease all;
      }
    }
  }
  .text {
    color: $text-color;
    font-size: 14 * 0.0625rem;
    a {
      color: $text-color;
      transition: 0.3s ease all;
      border-bottom: 1px solid rgba($text-color, 0.2);
      padding: 2px 0;
      font-weight: 600;
      &:hover {
        color: $primary-color;
        border-color: $primary-color;
      }
    }
  }
}
