.custom-time-input {
  input[type="time"] {
    padding-right: 60px;
  }
  &::after {
    content: "";
    position: absolute;
    right: 25px;
    top: 50%;
    width: 16px;
    height: 16px;
    transform: translateY(-50%);
    @include background-image(
      "data:image/svg+xml;base64,"
    );
  }
}
