.b--text {
  &--size {
    &-small span {font-size: ($font-size - 2) * 0.0625rem;}
    &-normal span {font-size: $font-size * 1rem;}
    &-big span {font-size: ($font-size + 2) * 0.0625rem;}
  }
  &--weight {
    &-bold span {font-weight: 600;}
    &-light span {font-weight: 300;}
  }
  span {
    display: block;
    transition: 0.3s ease all;
    white-space: nowrap;
    position: relative;
    z-index: 2;
    backface-visibility: hidden;
  }
}
