.custom-checkbox-input {
  position: relative;
  input {
    @include absolute-element;
    padding: 0;
    margin: 0;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
    cursor: pointer;
    &:hover {
      & + .checkbox {
        .box {
          border-color: rgba(#fff, 1);
        }
      }
    }
    &:checked {
      & + .checkbox {
        .box {
          &::before {
            opacity: 1;
            transform: translateX(-50%) translateY(-50%) scale(1);
          }
        }
        .text {
          span {
            color: $primary-color;
          }
        }
      }
    }
  }
  .checkbox {
    display: flex;
    align-items: center;
    .box {
      flex-shrink: 0;
      border: 1px solid rgba(#fff, 0.75);
      width: 18px;
      height: 18px;
      border-radius: $checkbox-border-radius;
      margin-right: 10px;
      transition: 0.3s ease all;
      cursor: pointer;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        border-radius: $checkbox-border-radius;
        transform: translateX(-50%) translateY(-50%) scale(0.75);
        opacity: 0;
        width: 8px;
        height: 8px;
        background-color: #fff;
        transition: 0.3s ease all;
      }
    }
    .text {
      cursor: pointer;
      span {
        color: $border-color;
        transition: 0.3s ease all;
      }
      strong {
        cursor: pointer;
        transition: .45s ease all;
        &:hover {
          color: $primary-color;
        }
      }
    }
  }
}
