.signature {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  transition: 0.45s;
  .penta-logo {
    display: flex;
    align-items: center;
    width: fit-content;
    min-width: 125px;
    line-height: 1;
    .penta-logo-brand {
      display: flex;
      align-items: center;
      .penta-logo-img {
        width: 33px;
        height: 30px;
        position: relative;
        .penta-logo-p {
          display: block;
          position: absolute;
          left: 0;
          top: 0;
          height: 30px;
        }
        .penta-logo-y {
          display: block;
          position: absolute;
          height: 10px;
          left: 10px;
          top: 16px;
          animation: penta-animation 2s linear infinite;
          transform-style: preserve-3d;
        }
      }
    }
    .penta-logo-text {
      font-weight: 500;
      max-width: 82px;
      font-family: "Segoe UI", Tahoma, Verdana, sans-serif;
      text-transform: uppercase;
      letter-spacing: 2.5px;
      margin-right: 5px;
      transform: scale(0.9) translateZ(0) translate3d(0, 0, 0) perspective(1px);
      text-align: right;
      transition: opacity 0.3s ease, max-width 0.3s ease;
      display: flex;
    }
    .penta-logo-brand {
      strong {
        font-weight: 500;
        overflow: hidden;
        max-width: 0;
        display: block;
        font-family: "Segoe UI", Tahoma, Verdana, sans-serif;
        text-transform: uppercase;
        letter-spacing: 2.5px;
        margin-left: 5px;
        transform: scale(0.9) translateZ(0) translate3d(0, 0, 0)
          perspective(1px);
        transition: max-width 0.3s ease;
      }
    }
    &.dark {
      .penta-logo-text {
        color: black;
      }
      .penta-logo-brand {
        strong {
          color: black;
        }
      }
    }
    &.light {
      .penta-logo-text {
        color: #fff;
      }
      .penta-logo-brand {
        strong {
          color: #fff;
        }
      }
    }
    &:hover {
      .penta-logo-brand {
        strong {
          max-width: 75px !important;
        }
      }
      .penta-logo-text {
        opacity: 0 !important;
        max-width: 0 !important;
      }
    }
  }
}

@keyframes penta-animation {
  from {
    transform: scale(0.85) rotateY(0deg) translateZ(0) translate3d(0, 0, 0)
      perspective(1px);
  }
  to {
    transform: scale(0.85) rotateY(-360deg) translateZ(0) translate3d(0, 0, 0)
      perspective(1px);
  }
}
