.header-space {
  padding-top: var(--header-height);
}

.sticky {
  position: sticky !important;
  top: 45px;
  transition: 0.45s ease all !important;
  &.active {
    top: 120px !important;
  }
}

.bg-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  transition: 0.45s;
  &.active {
    opacity: 1;
    visibility: visible;
  }
  &--white {
    background: linear-gradient(
      90deg,
      rgba(#fff, 0) 0%,
      rgba(#fff, 0.8) 30%,
      rgba(#fff, 0.95) 100%
    );
  }
  &--black {
    background: linear-gradient(
      90deg,
      rgba(#000, 0.8) 0%,
      rgba(#000, 0.8) 30%,
      rgba(#000, 0.8) 100%
    );
  }
  &--transparent {
    background: transparent;
  }
}

.ripple {
  position: relative;
  display: flex;
  &:before,
  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    box-shadow: 0 0 2px #e5e5e5 !important;
    pointer-events: none;
    transition: 0.25s ease box-shadow;
  }
  &:before {
    animation: ripple 2s linear infinite;
  }
  &:after {
    animation: ripple 2s linear 1s infinite;
  }
  &--primary {
    &:before,
    &:after {
      box-shadow: 0 0 1px $primary-color !important;
    }
  }
  &--secondary {
    &:before,
    &:after {
      box-shadow: 0 0 1px $secondary-color !important;
    }
  }
}

.fill {
  &--primary {
    fill: $primary-color !important;
  }
  &--secondary {
    fill: $secondary-color !important;
  }
  &--white {
    fill: #fff !important;
  }
  &--black {
    fill: #000 !important;
  }
}

.stroke {
  &--primary {
    stroke: $primary-color !important;
  }
  &--secondary {
    stroke: $secondary-color !important;
  }
  &--white {
    stroke: #fff !important;
  }
  &--black {
    stroke: #000 !important;
  }
}

.no-content-field {
  .no-content {
    font-size: 18px;
    text-align: center;
    color: $primary-color;
    border: 1px solid $primary-color;
    padding: 15px;
    border-radius: 100px;
    transition: 0.45s;
    &:hover {
      color: #fff;
      border-color: $primary-color;
      background: $primary-color;
      box-shadow: $box-shadow-primary !important;
    }
  }
}

.page-content {
  padding-top: $page-content-padding;
  padding-bottom: $page-content-padding;
}

.form-send-icon {
  max-width: 150px;
  margin-bottom: 30px;
}

.title-section {
  display: grid;
  grid-template-columns: minmax(0, 1fr);
  grid-gap: 36px;
  position: relative;
  z-index: 1;

  &--two {
    grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
    grid-gap: 36px;
  }
  .title-field {
    align-self: center;
    .title {
      font-size: 40px;
      color: #e3e3e3;
      line-height: 1.25;
      font-weight: bold;
    }
  }
  .controller-field {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .controller-navigation {
      display: flex;
      justify-content: flex-end;
      .homepage-latest-next,
      .homepage-latest-prev,
      .homepage-project-next,
      .homepage-project-prev,
      .similar-next,
      .similar-prev {
        cursor: pointer;
        display: flex;
        align-items: center;
        position: relative;
        .icon {
          display: flex;
          align-items: center;
          @include svg-color(rgba(#fff, 0.25));
          transition: 0.45s;
        }
        &:hover {
          .icon {
            @include svg-color($primary-color);
          }
        }
      }
      .homepage-latest-prev,
      .homepage-project-prev,
      .similar-prev {
        margin-right: 25px;
        padding-right: 25px;
        &:before {
          content: "";
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 1px;
          height: 100%;
          background: rgba(#707070, 0.15);
          pointer-events: none;
        }
        .icon {
          transform: rotate(180deg);
        }
        &:hover {
          .icon {
            transform: translateX(-3px) rotate(180deg);
          }
        }
      }
      .homepage-latest-next,
      .homepage-project-next,
      .similar-next {
        &:hover {
          .icon {
            transform: translateX(3px);
          }
        }
      }
      .swiper-button-disabled {
        cursor: no-drop;
        .icon {
          @include svg-color(rgba(#fff, 0.2));
        }
        &.similar-prev {
          &:hover {
            .icon {
              transform: rotate(180deg);
            }
          }
        }
        &:hover {
          .icon {
            transform: unset;
            @include svg-color(rgba(#fff, 0.2));
          }
        }
      }
    }
    .link-field {
      margin-left: 20px;
      a {
        padding-bottom: 5px;
        position: relative;
        .text {
          font-size: 16px;
          color: #fff;
          line-height: 1.5;
          font-weight: 500;
          transition: 0.45s;
        }
        &:before {
          content: "";
          position: absolute;
          left: 0;
          bottom: 0;
          width: 0;
          height: 2px;
          background: $primary-color;
          transition: 0.45s;
        }
        &:hover {
          &:before {
            width: 100%;
          }
          .text {
            color: $primary-color;
          }
        }
      }
    }
  }
}

.more-field {
  padding-bottom: 10px;
  .container {
    .wrapper {
      .more {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: fit-content;
        margin: auto;
        .text {
          font-size: 20px;
          color: #e3e3e3;
          transition: 0.45s ease all;
        }
        .icon {
          margin-top: 10px;
          @include svg-color(#e3e3e3);
          transition: 0.45s ease all;
        }
        &:hover {
          .text {
            color: #fff;
          }
          .icon {
            transform: translateY(10px);
            @include svg-color(#fff);
          }
        }
      }
    }
  }
}

.active-text {
  $delayField: (
    "1": 850ms,
    "2": 950ms,
    "3": 1050ms,
    "4": 1150ms,
    "5": 1250ms,
    "6": 1350ms,
    "7": 1450ms,
    "8": 1550ms,
    "9": 1650ms,
    "10": 1750ms,
  );

  @each $delay, $duration in $delayField {
    .anim {
      &:nth-child(#{$delay}) {
        opacity: 0;
        animation-name: menuAnimateIn;
        animation-timing-function: cubic-bezier(0.68, 0, 0.265, 1);
        animation-fill-mode: both;
        animation-duration: $duration;
      }
    }
  }
  @keyframes menuAnimateIn {
    0% {
      transform: translate3d(0, 100px, 0);
      opacity: 0;
    }
    99% {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
    100% {
      transform: translate3d(0, 0, 0);
      opacity: 1;
      display: none;
    }
  }
}
