.contact-form-field {
  position: relative;
  &:hover {
    .shape {
      opacity: 0.2;
    }
    .container {
      .wrapper {
        .text-field {
          .mini-text {
            &:before {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .shape {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0.1;
    max-width: $default-container;
    width: 100%;
    height: 100%;
    transition: 0.45s ease all;
    pointer-events: none;
    .img {
      height: 100%;
      width: 100%;
    }
  }
  .container {
    .wrapper {
      .text-field {
        margin-bottom: 80px;
        .mini-text {
          font-size: 16px;
          color: $primary-color;
          line-height: 1.5;
          font-weight: bold;
          position: relative;
          width: fit-content;
          margin: auto;
          padding-bottom: 10px;
          margin-bottom: 50px;
          &:before {
            content: "";
            position: absolute;
            width: 50%;
            height: 3px;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            background: $primary-color;
            transition: 0.45s ease all;
          }
        }
        .title {
          font-size: 36px;
          color: #e3e3e3;
          line-height: 1.25;
          font-weight: bold;
          margin-bottom: 30px;
        }
        .description {
          font-size: 18px;
          color: #e3e3e3;
          line-height: 1.5;
        }
      }
      .form {
        .item {
          display: grid;
          grid-template-columns: minmax(0, 3fr) minmax(0, 6fr);
          border-bottom: 1px solid rgba(255, 255, 255, 0.3);
          transition: 0.45s ease all;
          &:before {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            width: 0;
            height: 1px;
            background: #fff;
            transition: 0.45s ease all;
          }
          &:hover {
            &:before {
              width: 100%;
            }
            label {
              color: rgba(#e3e3e3, 1);
            }
          }
          input:not([type="radio"]),
          input:not([type="checkbox"]),
          textarea,
          select {
            box-shadow: unset;
            border-radius: 0;
            transition: 0.45s ease all;
            &::placeholder {
              color: rgba(#e3e3e3, 0.25);
            }
            &:hover,
            &:focus {
              &::placeholder {
                color: rgba(#e3e3e3, 1);
              }
            }
          }
          &.custom-select {
            .select {
              grid-column: span 2;
              padding-left: 0;
            }
          }
          &.custom-checkbox-input {
            border-bottom: unset;
            align-self: center;
            &:before {
              display: none;
            }
            .gdpr-field {
              grid-column: span 2;
              label {
                font-size: 14px;
              }
            }
          }
          &.button-field {
            border-bottom: unset;
            display: flex;
            justify-content: center;
            align-items: center;
            &:before {
              display: none;
            }
          }
          textarea {
            height: $input-height;
          }
          label {
            color: rgba(#e3e3e3, 0.75);
            font-size: 18px;
            line-height: 1.5;
            align-self: center;
            transition: 0.45s;
          }
        }
      }
    }
  }
}

.map-field {
  mask-image: url("../../assets/image/static/map-mask.svg");
  mask-position: bottom center;
  mask-repeat: no-repeat;
  mask-size: cover;
  background: transparent;
  height: calc(100vh - $page-content-padding);
  max-height: 1200px;
  min-height: 930px;
  padding: 0 !important;
  position: relative;
  .wrapper {
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    pointer-events: none;
    .image-field {
      height: 100%;
      .img {
        height: 100%;
        position: relative;
      }

      .text-container {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 5;
        left: 0;
        right: 0;
        margin: auto;
        .text-wrapper {
          display: grid;
          grid-template-columns: minmax(0, 5fr) minmax(0, 7fr);
          .content {
            pointer-events: all;
            .contact-info {
              margin-bottom: 30px;
              .main-title {
                font-size: 36px;
                color: $primary-color;
                line-height: 1.25;
                font-weight: bold;
                margin-bottom: 20px;
              }
              .location {
                font-size: 30px;
                color: #111;
                line-height: 1.25;
                margin-bottom: 10px;
                strong {
                }
              }
              ul {
                li {
                  margin-bottom: 10px;
                  &:last-child {
                    margin-bottom: 0;
                  }
                  a {
                    display: flex;
                    flex-direction: column;
                    .title {
                      margin-bottom: 10px;
                      font-size: 18px;
                      color: #111;
                      font-weight: 600;
                      margin-bottom: 5px;
                      transition: 0.45s ease all;
                    }
                    .text {
                      margin-bottom: 10px;
                      font-size: 18px;
                      color: #111;
                      margin-bottom: 5px;
                      transition: 0.45s ease all;
                    }
                    &:hover {
                      .title {
                        color: $primary-color;
                      }
                      .text {
                        color: $primary-color;
                        transform: translateX(3px);
                      }
                    }
                  }
                }
              }
            }
            .team-info {
              ul {
                display: grid;
                grid-template-columns: repeat(2, minmax(0, 1fr));
                grid-gap: 50px;
                li {
                  &:hover {
                    .image {
                      .img {
                        border-color: $primary-color;
                        img {
                          transform: scale(1.2);
                        }
                      }
                    }
                    .text-field {
                      .title {
                        color: $primary-color;
                      }
                      &:after {
                        height: 100%;
                      }
                    }
                  }
                  .image {
                    margin-bottom: 10px;
                    .img {
                      border-radius: 100%;
                      width: 100px;
                      height: 100px;
                      border: 2px solid transparent;
                      transition: 0.45s;
                      img {
                        transition: 0.45s ease all;
                      }
                    }
                  }
                  .text-field {
                    position: relative;
                    padding-left: 25px;
                    &:before {
                      content: "";
                      position: absolute;
                      left: 0;
                      top: 0;
                      width: 4px;
                      height: 100%;
                      border-radius: 20px;
                      background: #111;
                    }
                    &:after {
                      content: "";
                      position: absolute;
                      left: 0;
                      top: 0;
                      width: 4px;
                      height: 0;
                      border-radius: 20px;
                      background: $primary-color;
                      transition: 0.45s;
                    }
                    .title {
                      font-size: 20px;
                      color: #111;
                      line-height: 1.5;
                      font-weight: 600;
                      margin-bottom: 10px;
                      transition: 0.45s;
                    }
                    .name {
                      font-size: 20px;
                      color: #111111;
                      line-height: 1.5;
                      font-weight: 300;
                      margin-bottom: 10px;
                    }
                    .mail {
                      font-size: 20px;
                      color: #111111;
                      line-height: 1.5;
                      font-weight: 300;
                      display: inline-flex;
                      transition: 0.45s ease all;
                      &:hover {
                        color: $primary-color;
                        transform: translateX(3px);
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

.map-field-inner {
  height: 100%;
  .img {
    height: 100%;
  }
  .map-selector {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(calc(-50% + 300px), -50%);
    display: flex;
    align-items: center;
    cursor: pointer;
    z-index: 20;
    background-color: $primary-color;
    padding: 30px 20px;
    border-radius: 20px;
    transition: 0.45s;
    &:hover {
      box-shadow: $box-shadow-primary-hover;
      a {
        transform: translateY(-3px);
      }
    }
    a {
      transition: 0.45s ease all;
      .logo {
        max-width: 100px;
        margin-bottom: 10px;
        padding-bottom: 10px;
        border-bottom: 1px solid #fff;
      }
      .road-text {
        font-size: 16px;
        color: #fff;
        line-height: 1.5;
        font-weight: 600;
        text-align: center;
      }
    }
