footer {
  transform: translateZ(0) translate3d(0, 0, 0);
  .footer-container {
    padding-top: calc(400px - $page-content-padding);
    position: relative;
    transform: translateZ(0) translate3d(0, 0, 0);
    .footer-background {
      position: absolute;
      left: 0;
      top: 0;
      pointer-events: none;
      z-index: -1;
      width: 100%;
      height: 100%;
      video {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      &:before {
        @include pseudo-element;
        top: -1px;
        height: 100%;
        background: linear-gradient(
          180deg,
          rgba(#000, 1) 0%,
          rgba(#000, .1) 50%,
          rgba(#000, 1) 100%
        );
        pointer-events: none;
      }
    }
    .footer-top {
      transform: translateZ(0) translate3d(0, 0, 0);
      .container {
        .wrapper {
          display: grid;
          grid-template-columns: minmax(0, 3fr) minmax(0, 9fr);
          grid-gap: 150px;
          padding: 40px 0;
          .footer-logo-field {
            .logo {
              margin-bottom: 20px;
            }
            .title {
              font-size: 16px;
              color: #fff;
              line-height: 1.5;
              font-weight: 600;
              margin-bottom: 20px;
            }
            .description {
              font-size: 16px;
              color: #fff;
              line-height: 1.5;
              font-weight: 300;
            }
          }
          .footer-menu-field {
            display: grid;
            grid-template-columns: minmax(0, 1fr) minmax(0, auto);
            .footer-menu {
              display: grid;
              grid-template-columns: repeat(3, minmax(0, auto));
              align-self: center;
              ul {
                margin-right: 50px;
                padding-right: 50px;
                position: relative;
                &:last-child {
                  margin-right: 0;
                  padding-right: 0;
                  &:before {
                    display: none;
                  }
                }
                &:before {
                  content: '';
                  position: absolute;
                  right: 0;
                  top: 50%;
                  transform: translateY(-50%);
                  width: 1px;
                  height: 100%;
                  background: rgba(#ffffff, 0.15);
                }
                li {
                  margin-bottom: 30px;
                  &:last-child {
                    margin-bottom: 0;
                  }
                  &:first-child {
                    a {
                      .text {
                        font-weight: bold;
                      }
                    }
                  }
                  a {
                    display: inline-flex;
                    transition: .45s ease all;
                    .text {
                      font-size: 20px;
                      color: #fff;
                      line-height: 1.5;
                      font-weight: 300;
                    }
                    &:hover {
                      transform: translateX(5px);
                    }
                  }
                }
              }
            }
            .footer-social-media {
              .social-media-ul {
                .sm-li {
                  margin-right: 0;
                  margin-bottom: 25px;
                  &:last-child {
                    margin-bottom: 0;
                  }
                }
              }
            }
          }
        }
      }
    }
    .footer-middle {
      transform: translateZ(0) translate3d(0, 0, 0);
      .container {
        .wrapper {
          display: grid;
          grid-template-columns: minmax(0, 5fr) minmax(0, 7fr);
          grid-gap: 36px;
          border-top: 1px solid rgba(#ffffff, 0.15);
          padding: 40px 0;
          .contact-field {
            ul {
              li {
                display: flex;
                align-items: center;
                margin-bottom: 30px;
                &:last-child {
                  margin-bottom: 0;
                }
                a {
                  display: flex;
                  align-items: center;
                  .icon {
                    font-size: 20px;
                    color: #e3e3e3;
                    line-height: 1.5;
                    margin-right: 15px;
                    min-width: 20px;
                  }
                  .text {
                    font-size: 20px;
                    color: #e3e3e3;
                    line-height: 1.5;
                  }
                  &:hover {
                    .icon {
                      color: $primary-color;
                    }
                    .text {
                      color: $primary-color;
                      transform: translateX(5px);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .footer-bottom {
      transform: translateZ(0) translate3d(0, 0, 0);
      .container {
        .wrapper {
          display: grid;
          grid-template-columns: repeat(2, minmax(0, 1fr));
          grid-gap: 30px;
          border-top: 1px solid rgba(#ffffff, 0.15);
          padding: 40px 0;
          .copyright {
            .text {
              color: #ffffff;
              font-size: 18px;
              line-height: 1.5;
              transition: 0.45s ease all;
              a {
                color: #ffffff;
                font-size: 18px;
                line-height: 1.5;
                font-weight: bold;
                transition: 0.45s ease all;
                &:hover {
                  color: darken(#fff, 20%);
                }
              }
              &:hover {
                color: #ffffff80;
              }
            }
          }
          .right-field {
            display: flex;
            justify-content: flex-end;
            ul {
              display: flex;
              margin-right: 30px;
              padding-right: 30px;
              position: relative;
              &:before {
                content: "";
                position: absolute;
                right: 0;
                top: 50%;
                width: 1px;
                height: 100%;
                transform: translateY(-50%);
                background: #fff;
              }
              li {
                margin-right: 30px;
                display: flex;
                align-items: center;
                &:last-child {
                  margin-right: 0;
                }
                a {
                  .text {
                    font-size: 18px;
                    color: #fff;
                    display: inline-flex;
                    line-height: 1.5;
                    &:hover {
                      color: darken(#fff, 20%);
                      transform: translateX(3px);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
