.about-text-field {
  padding-bottom: calc($page-content-padding * 2);
  &:hover {
    .container {
      .wrapper {
        .text-field {
          .mini-text {
            &:before {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .container {
    .wrapper {
      .text-field {
        .mini-text {
          font-size: 16px;
          color: $primary-color;
          line-height: 1.5;
          font-weight: bold;
          position: relative;
          width: fit-content;
          margin: auto;
          padding-bottom: 10px;
          margin-bottom: 40px;
          &:before {
            content: "";
            position: absolute;
            width: 50%;
            height: 3px;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            background: $primary-color;
            transition: 0.45s;
          }
        }
        .title {
          font-size: 36px;
          color: #fff;
          line-height: 1.25;
          font-weight: bold;
          margin-bottom: 50px;
        }
        p {
          font-size: 24px;
          color: #e3e3e3;
          line-height: 2;
        }
      }
    }
  }
}

.about-company-field {
  background: #1a1a1a;
  &:hover {
    .container {
      .wrapper {
        .text-field {
          .mini-text {
            &:before {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: minmax(0, 5fr) minmax(0, 7fr);
      grid-gap: 100px;
      .text-field {
        .mini-text {
          font-size: 16px;
          color: $primary-color;
          line-height: 1.5;
          font-weight: bold;
          position: relative;
          width: fit-content;
          padding-bottom: 10px;
          margin-bottom: 40px;
          &:before {
            content: "";
            position: absolute;
            width: 50%;
            height: 3px;
            bottom: 0;
            left: 0;
            background: $primary-color;
            transition: 0.45s;
          }
        }
        .title {
          font-size: 36px;
          color: #fff;
          line-height: 1.25;
          font-weight: bold;
          margin-bottom: 50px;
        }
        p {
          font-size: 24px;
          color: #e3e3e3;
          line-height: 2;
        }
      }
      .image-field {
        position: relative;
        .image {
          position: absolute;
          &--one {
            right: 0;
            top: -$page-content-padding * 2;
            .img {
              height: 600px;
              border-radius: $image-radius;
            }
          }
          &--two {
            left: 0;
            /* bottom: -$page-content-padding; */
            top: -120px;
            width: 100%;
            max-width: 475px;
            z-index: 20;
            .img {
              @include svg-color(#1a1a1a);
              svg {
                height: 350px;
                width: 100%;
                path {
                  stroke-width: 25px;
                  fill: none;
                  stroke-miterlimit: 10;
                }
                image {
                  width: 100%;
                  height: 100%;
                }
              }
            }
          }
        }
      }
    }
  }
}

.about-testimonials-field {
  &:hover {
    .container {
      .wrapper {
        .text-field {
          .seperator {
            filter: grayscale(0);
          }
        }
      }
    }
  }
  .container {
    .wrapper {
      .text-field {
        display: flex;
        .seperator {
          display: flex;
          align-items: center;
          filter: grayscale(1);
          transition: 0.45s;
          &:last-child {
            transform: rotate(180deg);
          }
        }
        p {
          font-size: 24px;
          color: #e3e3e3;
          line-height: 2;
          text-align: center;
          padding-left: 70px;
          padding-right: 70px;
        }
      }
    }
  }
}
.about-mission-field {
  .container {
    .wrapper {
      background: #1c1c1c;
      border-radius: $image-radius;
      display: grid;
      grid-template-columns: minmax(0, 6fr) minmax(0, 6fr);
      grid-gap: 100px;
      overflow: hidden;
      .text-field {
        padding: 60px 0 60px 60px;
        ul {
          li {
            margin-bottom: 100px;
            padding-left: 40px;
            position: relative;
            &:last-child {
              margin-bottom: 0;
            }
            &.active,
            &:hover {
              &:after {
                height: 100%;
              }
              .title {
                color: $primary-color;
                -webkit-text-stroke: 1px rgba(0, 0, 0, 0);
              }
              .desc {
                color: #fff;
              }
            }
            &:before {
              content: "";
              position: absolute;
              left: 0;
              top: 0;
              width: 10px;
              height: 100%;
              border-radius: 20px;
              background: #e3e3e3;
            }
            &:after {
              content: "";
              position: absolute;
              left: 0;
              top: 0;
              width: 10px;
              height: 0;
              border-radius: 20px;
              background: $primary-color;
              transition: 0.45s;
            }
            .title {
              font-size: 36px;
              line-height: 1.25;
              color: transparent;
              -webkit-text-stroke: 1px rgba(255, 255, 255, 0.5);
              transition: 0.45s ease all;
              font-weight: bold;
              margin-bottom: 20px;
            }
            .desc {
              font-size: 20px;
              color: #e3e3e3;
              line-height: 2;
              transition: 0.45s ease all;
            }
          }
        }
      }
      .image-field {
        position: relative;
        padding: 60px 60px 60px 0;
        .image {
          &--rope {
            position: absolute;
            right: -50%;
            top: 50%;
            transform: translateY(-50%) translateX(-20%);
            z-index: 5;
          }
          &--shape {
            position: absolute;
            height: 200%;
            right: -50%;
            top: 50%;
            transform: translateY(-50%) translateX(10%);
            opacity: 0.1;
            pointer-events: none;
            z-index: 2;
            transition: 0.45s;
            .img {
              height: 100%;
            }
          }
        }
      }
    }
  }
}
