@media only screen and (max-width: 1366px) {
  .project-filter-field .container .wrapper {grid-template-columns: minmax(0, 1fr);grid-gap: 24px;}
  .project-filter-field .container .wrapper .title-field .title {text-align: center;}
  .project-filter-field .container .wrapper .title-field .title::before {display: none;}

  .homepage-three-d-field .three-d-field,
  .threed-field .obj-field,
  .homepage-three-d-field .three-d-field .wrapper .image-field,
  .threed-field,
  .threed-field .placeholder-image .main-image .img {height: 400px;}
  .homepage-three-d-field .three-d-field .wrapper .wave {height: calc(400px / 2);}
}
