@for $i from 10 through 90 {
  @if $i % 2 == 0 {
    .fs--#{$i} {font-size: $i * 0.0625rem !important;}
  }
}

@media only screen and (max-width: 1440px) {
  @for $i from 10 through 90 {
    @if $i % 2 == 0 {
      @if $i > 16 {
        .fs--#{$i} {font-size: ($i - 2) * 0.0625rem !important;}
      }
    }
  }
}

