.img {
  overflow: hidden;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 100%;
    height: 100%;
  }
  &--radius {border-radius: $image-radius;}
  &.radius {border-radius: $image-radius;}
  &--cover {img {object-fit: cover;}}
  &--contain {img {object-fit: contain;}}
  &--scale {img {transition: 0.45s;}&:hover {img {transform: scale(1.1);}}}
  &--kenburn {img {transition: 5s;}&:hover {img {transform: scale(1.1);}}}
}
