.menu-field {
  .menu-ul {
    display: flex;
    .menu-li {
      margin-right: 50px;
      align-self: center;
      &:last-child {
        margin-right: 0;
      }
      &.has-menu {
        &.active {
          .menu-selector {
            .inner {
              transform: rotate(45deg);
              &:before {
                opacity: 0;
              }
              &:after {
                bottom: 0;
                transform: rotate(90deg);
              }
            }
          }
        }
      }
      .menu-link {
        display: flex;
        align-items: center;
        &:hover {
          .text {
            color: $primary-color;
          }
          .menu-selector {
            .inner {
              background: $primary-color;
              &:before,
              &:after {
                background: $primary-color;
              }
            }
          }
        }
        .text {
          font-size: 20px;
          color: #fff;
          line-height: 1.5;
          font-weight: 500;
        }
        .icon {
          display: flex;
          align-items: center;
          margin-left: 30px;
        }
        .menu-selector {
          height: 30px;
          display: flex;
          align-items: center;
          order: 2;
          cursor: pointer;
          .inner {
            width: 30px;
            height: 2px;
            background: #fff;
            position: relative;
            transition: 0.45s;
            &:after,
            &:before {
              content: "";
              width: 30px;
              height: 2px;
              background: #fff;
              position: absolute;
            }
            &:before {
              top: -8px;
              opacity: 1;
              transition: 0.45s;
            }
            &:after {
              bottom: -8px;
              transition: 0.45s;
            }
          }
        }
      }
    }
  }
}

.mega-menu {
  position: fixed;
  right: 0;
  top: 0;
  background: $primary-color;
  height: 100vh;
  border-radius: 30px 0 0 30px;
  overflow: hidden;
  max-width: $default-container;
  transform: translateX(100%);
  transition: .6s cubic-bezier(0.77, 0, 0.175, 1) all;
  &.active {
    transform: translateX(0%);
  }
  .close {
    position: absolute;
    right: 30px;
    top: 30px;
    z-index: 25;
    display: flex;
    align-items: center;
    cursor: pointer;
    &:hover {
      svg {
        transform: rotate(180deg);
        @include svg-color(darken(#fff, 10%));
      }
    }
    svg {
      @include svg-color(#fff);
      width: 24px;
      transition: 0.45s;
    }
  }
  .shape {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    opacity: 0.15;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .background-color {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(#000, 0.5);
    z-index: 15;
  }
  .mega-menu-inner {
    position: relative;
    z-index: 20;
    display: grid;
    grid-template-columns: minmax(0, auto) minmax(0, 1fr);
    grid-gap: 70px;
    height: 100%;
    align-items: center;
    .mega-menu-field {
      padding: 0 0 0 150px;
      .mm-ul {
        .mm-li {
          margin-bottom: 50px;
          &:last-child {
            margin-bottom: 0;
          }
          .mm-link {
            display: flex;
            align-items: center;
            .text {
              font-size: 40px;
              color: #e3e3e3;
              font-weight: bold;
              transition: 0.45s;
            }
            &:hover {
              .text {
                color: $primary-color;
                transform: translateX(5px);
              }
            }
          }
        }
      }
    }
    .image-field {
      .img {
        mask-image: url("../../assets/image/static/menu-mask.svg");
        mask-position: center center;
        mask-repeat: no-repeat;
        mask-size: contain;
        background: transparent;
        height: 500px;
        opacity: 1;
        img {
          transform: scale(1);
          opacity: 1;
          transition: 0.45s ease all;
        }
        &.active {
          img {
            transform: scale(1.1);
            opacity: 0;
          }
        }
      }
    }
  }
}

.contact-menu {
  order: 2;
}

.has-menu {
  order: 3;
  margin-right: 0 !important;
}

.language {
  order: 1;
  margin-right: 50px !important;
  .language-field {
    display: flex;
    border-radius: 50px;
    overflow: hidden;
    .menu-link {
      display: flex;
      align-items: center;
      padding: 10px 20px;
      &.active {
        background: $primary-color;
        .text {
          color: #fff !important;
        }
      }
      .text {
        font-size: 18px;
        color: #e3e3e3;
        font-weight: bold;
        transition: 0.45s;
      }
      &:hover {
        .text {
          color: $primary-color;
          transform: translateX(5px);
        }
      }
    }
  }
}
