.custom-range-input {
  position: relative;
  input {
    display: block;
    margin: 0;
    position: relative;
    z-index: 1;
    appearance: none;
    width: 100%;
    background: transparent;
    &::-webkit-slider-thumb {appearance: none;}
    &:focus {outline: none;}
    &::-ms-track {width: 100%;cursor: pointer;background: transparent;border-color: transparent;color: transparent;}
    &::-webkit-slider-thumb {
      appearance: none;
      height: 30px;
      width: 30px;
      border-radius: 100%;
      background-color: $primary-color;
      border: 2px solid $border-color;
      border: 0;
      cursor: pointer;
      box-shadow: 0 0 0 6px #f8f8f8;
      margin-top: -1px;
    }
    &::-moz-range-thumb,
    &::-ms-thumb {
      border: 2px solid $border-color;
      height: 14px;
      width: 14px;
      border-radius: 100%;
      background-color: white;
      cursor: pointer;
    }
  }
  .bar {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 12px;
    cursor: pointer;
    background-color: $border-color;
    border-radius: 12px;
    .filled {
      @include absolute-element;
      height: calc(100% + 2px);
      top: -1px;
      border-radius: 12px;
      width: 0;
      background-color: $primary-color;
    }
  }
}
