.m-auto {
  margin: auto;
}
@for $i from 0 through 250 {
  @if $i % 5 == 0 {
    $value: $i * 1px;
    .m--#{$i} {margin: $value !important;}
    .mt--#{$i} {margin-top: $value !important;}
    .mb--#{$i} {margin-bottom: $value !important;}
    .ml--#{$i} {margin-left: $value !important;}
    .mr--#{$i} {margin-right: $value !important;}
  }
}

@media only screen and (max-width: 1680px) {
  @for $i from 0 through 250 {
    @if $i % 5 == 0 {
      $value: $i * 1px;
      @if $i > 180 {
        .m--#{$i} {margin: 180px !important;}
        .mt--#{$i} {margin-top: 180px !important;}
        .mb--#{$i} {margin-bottom: 180px !important;}
        .ml--#{$i} {margin-left: 180px !important;}
        .mr--#{$i} {margin-right: 180px !important;}
      }
    }
  }
}

