header {
  z-index: 100;
  position: fixed;
  width: 100%;
  height: var(--header-height);
  left: 0;
  top: 0;
  transition: 0.45s ease all;
  will-change: backdrop-filter, filter, height, background-color;
  backface-visibility: hidden;
  &.no-scroll {
    position: absolute;
    transform: unset !important;
    box-shadow: unset !important;
    height: var(--header-height) !important;
  }
  &.is-fixed {
    height: calc(var(--header-height) * 0.75);
    background-color: rgba(black, 0.5);
    backdrop-filter: blur(10px);
  }
  &.hidden {
    transform: translateY(-100%);
  }
  &.black {
    .menu-field .menu-ul .menu-li .menu-link .text{color: #111;}
    .menu-field .menu-ul .menu-li .menu-link .menu-selector .inner,
    .menu-field .menu-ul .menu-li .menu-link .menu-selector .inner:before,
    .menu-field .menu-ul .menu-li .menu-link .menu-selector .inner:after {background: #111;}
    .container .wrapper .logo-field img {transition: .45s;}
    &.is-fixed {
      .menu-field .menu-ul .menu-li .menu-link .text {color: #fff;}
      .menu-field .menu-ul .menu-li .menu-link .menu-selector .inner,
      .menu-field .menu-ul .menu-li .menu-link .menu-selector .inner:before,
      .menu-field .menu-ul .menu-li .menu-link .menu-selector .inner:after {background: #fff;}
      .container .wrapper .logo-field img {filter: invert(1);transition: .45s;}
    }
  }
  .container {
    height: 100%;
    .wrapper {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      .logo-field {
        max-width: 220px;
      }
    }
  }
}
