@for $i from 0 through 250 {
  @if $i % 5 == 0 {
    $value: $i * 1px;
    .p--#{$i} {padding: $value !important;}
    .pt--#{$i} {padding-top: $value !important;}
    .pb--#{$i} {padding-bottom: $value !important;}
    .pl--#{$i} {padding-left: $value !important;}
    .pr--#{$i} {padding-right: $value !important;}
  }
}

