.hr-job-field {
  .container {
    .wrapper {
      .job-field {
        ul {
          border: 1px solid rgba(112, 112, 112, 0.2);
          border-radius: $image-radius;
          overflow: hidden;
          li {
            display: grid;
            grid-template-columns: repeat(3, minmax(0, 1fr));
            grid-gap: 30px;
            border-bottom: 1px solid rgba(112, 112, 112, 0.2);
            padding: 50px 30px;
            transition: 0.45s;
            &:hover {
              background: $primary-color;
            }
            .title-field {
              align-self: center;
              .title {
                font-size: 26px;
                font-weight: 600;
                line-height: 1.5;
                color: #e3e3e3;
                transition: 0.45s;
              }
            }
            .position-info-field {
              align-self: center;
              justify-self: center;
              .info {
                font-size: 16px;
                line-height: 1.5;
                font-weight: 500;
                color: #e3e3e3;
                text-align: center;
                transition: 0.45s;
              }
            }
            .select-field {
              align-self: center;
              justify-self: flex-end;
            }
            &.disabled {
              cursor: no-drop;
              .select-field {
                .button {
                  pointer-events: none;
                }
              }
            }
          }
        }
      }
    }
  }
}

.hr-form-field {
  position: relative;
  overflow: hidden;
  .container {
    .background-image {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      opacity: 0.25;
      pointer-events: none;
      &:before {
        @include pseudo-element;
        background: linear-gradient(
          270deg,
          rgba(#000, 0) 0%,
          rgba(#000, 1) 100%
        );
        z-index: 1;
      }
      .img {
        &:before {
          @include pseudo-element;
          background: radial-gradient(rgba(#000, 0) 60%, #000 70%);
          z-index: 1;
        }
      }
    }
    .background-shape {
      position: absolute;
      left: -50%;
      top: 50%;
      transform: translateY(-50%) translateX(calc(100% - 20%));
      opacity: 0.25;
      pointer-events: none;
    }
    .container {
      .wrapper {
        .form {
          .item {
            display: grid;
            grid-template-columns: minmax(0, 3fr) minmax(0, 6fr);
            border-bottom: 1px solid rgba(255, 255, 255, 0.3);
            transition: 0.45s;
            &:before {
              content: "";
              position: absolute;
              left: 0;
              bottom: 0;
              width: 0;
              height: 1px;
              background: #fff;
              transition: 0.45s;
            }
            &:hover {
              &:before {
                width: 100%;
              }
              label {
                color: rgba(#e3e3e3, 1);
              }
            }
            input:not([type="radio"]),
            input:not([type="checkbox"]),
            textarea,
            select {
              box-shadow: unset;
              border-radius: 0;
              transition: 0.45s;
              &::placeholder {
                color: rgba(#e3e3e3, 0.25);
              }
              &:hover,
              &:focus {
                &::placeholder {
                  color: rgba(#e3e3e3, 1);
                }
              }
            }
            &.custom-select {
              .select {
                grid-column: span 2;
                padding-left: 0;
              }
            }
            &.custom-file {
              border-bottom: unset;
              &:before {
                display: none;
              }
              .button-field {
                grid-column: span 2;
                .your-cv {
                  .form-file-upload {
                    height: 100%;
                  }
                }
              }
            }
            &.custom-checkbox-input {
              border-bottom: unset;
              align-self: center;
              &:before {
                display: none;
              }
              .gdpr-field {
                grid-column: span 2;
                label {
                  font-size: 14px;
                }
              }
            }
            &.button-field {
              border-bottom: unset;
              display: flex;
              justify-content: flex-end;
              align-items: center;
              &:before {
                display: none;
              }
            }
            textarea {
              height: $input-height;
            }
            label {
              color: rgba(#e3e3e3, 0.75);
              font-size: 18px;
              line-height: 1.5;
              align-self: center;
              transition: 0.45s;
            }
          }
        }
      }
    }
  }
}
