.custom-file {
  position: relative;
  .file {
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    transition: 0.25s ease all;
    padding: 0 20px;
    cursor: pointer;
    box-shadow: inset 0 0 0 1px rgba($primary-color, 0.25);
    &::before {
      background-color: $primary-color;
    }
    span {
      color: $primary-color;
    }
    svg {
      fill: $primary-color;
    }
    &:hover {
      box-shadow: inset 0 0 0 1px $primary-color;
      span {
        color: white;
      }
      svg {
        fill: white;
      }
    }
    span {
      color: lighten(black, 40%);
      transition: 0.25s ease color;
      pointer-events: none;
      display: block;
      white-space: nowrap;
    }
  }
  .file-name {
    white-space: nowrap;
    overflow: hidden;
    width: calc(100% - 40px);
    height: 26px;
    text-overflow: ellipsis;
    font-size: 0.875rem;
    color: $primary-color;
    position: absolute;
    left: 20px;
    right: 20px;
    text-align: center;
    bottom: 15px;
  }
  input {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    cursor: pointer;
    z-index: 2;
    &::-webkit-file-upload-button {
      cursor: pointer;
    }
    &:hover,
    &.focus {
      & + .button {
        &::before {
          transform: translateX(0%);
        }
        box-shadow: inset 0 0 0 1px $primary-color;
        span {
          color: white;
        }
        svg {
          fill: white;
        }
      }
    }
  }
  .button-field {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
    border: 2px dashed rgba(#fff, 0.75);
    border-radius: 10px;
    width: 100%;
    transition: 0.45s;
    &:hover {
      border-color: $primary-color;
      .upload-button {
        .text {
          color: $primary-color;
        }
      }
      .info {
        color: $primary-color;
      }
    }
    .upload-button {
      margin-bottom: 20px;
      display: flex;
      .text {
        font-size: 16px;
        color: #fff;
        line-height: 1.5;
        transition: 0.45s;
      }
      .icon {
        display: flex;
        align-items: center;
        margin-left: 10px;
        @include svg-color($primary-color);
        transition: 0.45s;
      }
    }
    .info {
      font-size: 14px;
      font-weight: 300;
      line-height: 1.5;
      color: rgba(#fff, 0.75);
      margin-bottom: 15px;
      transition: 0.45s;
    }
  }
}
