@media only screen and (max-width: 768px) {
  footer .footer-container .footer-top .container .wrapper .footer-menu-field .footer-menu {grid-template-columns: repeat(2, minmax(0, auto));}
  footer .footer-container .footer-top .container .wrapper .footer-menu-field .footer-menu ul:last-child {display: none;}
  footer .footer-container .footer-top .container .wrapper .footer-menu-field .footer-menu ul:nth-child(2) {padding-right: 0;margin-right: 0;}
  footer .footer-container .footer-top .container .wrapper .footer-menu-field .footer-menu ul:nth-child(2):before {display: none;}
  footer .footer-container .footer-bottom .container .wrapper .right-field {flex-direction: column;}
  footer .footer-container .footer-bottom .container .wrapper .right-field ul {margin-right: 0;padding-right: 0;justify-content: center;margin-bottom: 20px;}
  .signature {justify-content: center;}
  footer .footer-container {padding-top: 100px;}
  footer .footer-container .footer-top .container .wrapper {padding-top: 0;}
  footer .footer-container .footer-bottom .container .wrapper .right-field ul:before {display: none;}
  .newsletter .form .form-title {text-align: center;}
  .newsletter .form {grid-template-columns: repeat(3, minmax(0, 1fr));}
  .popup {padding: 20px;}
  .newsletter .form .button-field .button {width: 100%;}
  .homepage-testimonial-field .container .wrapper .text-field p {padding-left: 30px;padding-right: 30px;}
  .about-company-field .container .wrapper .image-field .image--two,
  .homepage-testimonial-field .container .wrapper .image-field .image--two {display: none;}
  .about-company-field .container .wrapper .image-field .image--one,
  .homepage-testimonial-field .container .wrapper .image-field .image--one {width: 100%;right: 0;}
  .about-company-field .container .wrapper .image-field .image--one .img,
  .homepage-testimonial-field .container .wrapper .image-field .image--one .img,
  .homepage-latest-field .container .wrapper .homepage-latest-carousel .swiper-wrapper .swiper-slide .latest-box,
  .homepage-testimonial-field .container .wrapper .image-field {height: 300px;}
  .title-section--two {grid-template-columns: minmax(0, 1fr);grid-gap: 24px;}
  .title-section .title-field .title {text-align: center;}
  .title-section .controller-field {justify-content: center;}
  .homepage-project-box .text-field .description {@include text-clamp(4);}
  .homepage-three-d-field .three-d-field .wrapper .image-field {width: 100%;}
  .about-mission-field .container .wrapper .text-field {padding: 40px;}
  .breadcrumb .wrapper .image-field .text-container .text-field .scroll .content .icon {width: 60px;height: 60px;}
  .breadcrumb .wrapper .image-field .text-container .text-field .scroll .content .icon svg {width: 16px;}
  .breadcrumb .wrapper .image-field .img {height: 350px;}
  .breadcrumb .wrapper .image-field .text-container {transform: translateY(calc(-50% + var(--header-height) / 1.5));}
  .mega-menu .mega-menu-inner .mega-menu-field .mm-ul .mm-li .mm-link .text {font-size: 24px;}
  .mega-menu .mega-menu-inner .mega-menu-field .mm-ul .language {margin-right: 0 !important;}
  .blog-field .container .wrapper {grid-template-columns: repeat(1, minmax(0, 1fr));}
  .blog-box .image-field .text-field {left: 20px;width: calc(100% - 40px);}
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container .text-wrapper,
  .project-detail-field .main-content .main-container .tab--two .wrapper .text-container .text-wrapper {grid-template-columns: minmax(0, 1fr);}
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container .text-wrapper .right-field {display: none;}
  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .swiper-wrapper .swiper-slide .content .image-field .text-container .text-wrapper .right-field .button-field {flex-direction: column;}
  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .swiper-wrapper .swiper-slide .content .image-field .text-container .text-wrapper .right-field .button-field .button-box {margin-right: 0;padding-right: 0;margin-bottom: 15px;}
  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .swiper-wrapper .swiper-slide .content .image-field .text-container .text-wrapper .right-field .button-field .button-box:last-child {margin-bottom: 0;}
  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .swiper-wrapper .swiper-slide .content .image-field .text-container .text-wrapper .right-field .button-field .button-box:before {display: none;}
  .project-detail-field {height: unset;min-height: 100vh;}
  .project-detail-field .tab-field {position: fixed;bottom: 0;width: 100%;}
  .project-detail-field .main-content .main-container .tab {min-height: 100vh;}
  .project-detail-field .main-content .main-container .tab--two .wrapper .image-wrapper {height: 500px;position: relative;}
  .popup-products {width: 100%;border-radius: 0;}
  .popup-products .inner {padding: 30px;}
  .popup-products .inner .content {min-width: 100%;max-width: 100%;}
  .popup-products .inner .content .form-field {margin-top: 30px;}
  .project-detail-field .tab-field .container .wrapper .tab-content ul li {margin-right: 0;}

  .homepage-three-d-field .three-d-field,
  .threed-field .obj-field,
  .threed-field .obj-field #model,
  .threed-field .obj-field #model .model-images,
  .homepage-three-d-field .three-d-field .wrapper .image-field,
  .threed-field,
  .threed-field .placeholder-image .main-image .img {height: 250px !important;}
  .homepage-three-d-field .three-d-field .wrapper .wave {height: calc(250px / 2) !important;}
}
