@media only screen and (max-width: 600px) {
  .newsletter .form .size--full {grid-column: span 3;}
  .form--three .item.size--one {grid-column: span 3;}
  .newsletter .form .button-field .button {height: $input-height;}
  footer .footer-container .footer-top .container .wrapper .footer-menu-field .footer-menu {grid-template-columns: minmax(0, 1fr);}
  footer .footer-container .footer-top .container .wrapper .footer-menu-field .footer-menu ul:nth-child(1) {padding-right: 0;margin-right: 0;}
  footer .footer-container .footer-top .container .wrapper .footer-menu-field .footer-menu ul:nth-child(1):before {display: none;}
  footer .footer-container .footer-top .container .wrapper .footer-menu-field .footer-menu ul:nth-child(2) {display: none;}
  .menu-field .menu-ul .menu-li.contact-menu {display: none;}
  .about-testimonials-field .container .wrapper .text-field .seperator,
  .homepage-testimonial-field .container .wrapper .text-field .seperator {display: none;}
  .about-testimonials-field .container .wrapper .text-field p,
  .homepage-testimonial-field .container .wrapper .text-field p {padding-left: 0;padding-right: 0;}
  .breadcrumb .wrapper .image-field .img {height: 300px;}
  .project-box .info-field {height: 300px;margin: 0;}
  .blog-content-field .container .wrapper .content-field .main-image .img {height: 250px;}
  .hr-form-field .container .container .wrapper .form .item {grid-template-columns: minmax(0, 1fr);}
  .hr-form-field .container .container .wrapper .form .item input {padding-left: 0;padding-right: 0;}
  .hr-form-field .container .container .wrapper .form .item textarea {height: 125px;padding-left: 0;padding-right: 0;}
  .hr-form-field .container .container .wrapper .form .item.button-field {justify-content: center;}
  .hr-form-field .container .background-shape {left: 50%;transform: translate(-50%, -50%);}
  .hr-form-field .container .container {padding: 0;}
  .map-field .wrapper .image-field .text-container .text-wrapper .content .team-info ul {grid-template-columns: repeat(1, minmax(0, 1fr));}
  .map-field-inner .map-selector {padding: 20px;transform: translate(-50%, -50%);}
  .map-field-inner .map-selector a .logo {max-width: 80px;}
  .map-field-inner .map-selector a .road-text {font-size: 14px;}
  .similar-project-box .image-field {width: 100%;}
  .similar-project-box .content:before {width: 100%;}
  .blog-box .image-field .text-field {grid-template-columns: minmax(0, 1fr);}
  .blog-box--single .image-field .text-field .right-field {display: none;}
  .blog-box--featured .image-field .text-field .right-field {display: none;}
  .breadcrumb .wrapper .image-field .text-container .text-field {grid-template-columns: minmax(0, 1fr);}
  .breadcrumb .wrapper .image-field .text-container .text-field .scroll {display: none;}
  .breadcrumb .wrapper .image-field .text-container .text-field .navigation .title {text-align: center;}
  .breadcrumb .wrapper .image-field .text-container .text-field .navigation ul {display: none;}
  .breadcrumb .wrapper .image-field .text-container {transform: translateY(calc(-50% + var(--header-height) / 2));}
  .blog-box--single .image-field .text-field .left-field .name {font-size: 22px;}


  .contact-form-field .container .wrapper .form .item {grid-template-columns: minmax(0, 1fr);}
  .contact-form-field .container .wrapper .form .item input {padding-left: 0;padding-right: 0;}
  .contact-form-field .container .wrapper .form .item textarea {height: 125px;padding-left: 0;padding-right: 0;}
  .contact-form-field .container .wrapper .form .item.button-field {justify-content: center;}
  .hr-form-field .container .background-shape {left: 50%;transform: translate(-50%, -50%);}
  .hr-form-field .container .container {padding: 0;}
}
