@media only screen and (max-width: 480px) {
  header .container .wrapper .logo-field {max-width: 170px;}
  .homepage-latest-field .container .wrapper .homepage-latest-carousel .swiper-wrapper .swiper-slide .latest-box:after {opacity: 1;visibility: visible;}
  .homepage-latest-field .container .wrapper .homepage-latest-carousel .swiper-wrapper .swiper-slide .latest-box .text-field {opacity: 1;transform: translateY(-70px);}
  .homepage-latest-field .container .wrapper .homepage-latest-carousel .swiper-wrapper .swiper-slide .latest-box .button-field {opacity: 1; visibility: visible;transform: translateY(0);}
  .popup-products .inner .content .form-field .item {grid-template-columns: minmax(0, 3fr);}
  .popup-products .inner .content .form-field .item input:not([type=radio]), .popup-products .inner .content .form-field .item input:not([type=checkbox]), .popup-products .inner .content .form-field .item textarea, .popup-products .inner .content .form-field .item select {padding: 0;}
}
