@media only screen and (max-width: 900px) {
  .homepage-slider-field .wrapper .main-carousel .swiper-wrapper .swiper-slide .image-field .text-container .text-wrapper {grid-template-columns: minmax(0, 1fr);}
  .homepage-slider-field .wrapper .main-carousel .carousel-controller .carousel-pagination {top: unset;bottom: 20px;transform: unset;}
  .homepage-slider-field .wrapper .main-carousel .carousel-controller .carousel-pagination .swiper-pagination {flex-direction: row;justify-content: center;}
  .homepage-slider-field .wrapper .main-carousel .carousel-controller .carousel-pagination .swiper-pagination .swiper-pagination-bullet {margin-bottom: 0;margin-right: 40px;}
  .homepage-slider-field .wrapper .main-carousel .carousel-controller .carousel-pagination .swiper-pagination .swiper-pagination-bullet:last-child {margin-right: 0;}
  .homepage-slider-field .wrapper .main-carousel .carousel-controller .carousel-pagination .swiper-pagination .swiper-pagination-bullet .content .value {top: -450%;left: 50%;transform: translateX(-50%);}
  .homepage-slider-field .wrapper .main-carousel .swiper-wrapper .swiper-slide .image-field .text-container .text-wrapper .content .title {margin-bottom: 20px;}
  .homepage-slider-field .wrapper .main-carousel .swiper-wrapper .swiper-slide .image-field .text-container .text-wrapper .content .description {margin-bottom: 40px;}
  .homepage-three-d-field .text-field .image img {max-width: 125px;}
  footer .footer-container .footer-bottom .container .wrapper {grid-template-columns: minmax(0, 1fr);grid-gap: 24px;}
  footer .footer-container .footer-bottom .container .wrapper .copyright .text {text-align: center;}
  footer .footer-container .footer-bottom .container .wrapper .right-field {justify-content: center;}
  .homepage-testimonial-field .container .wrapper .text-field p {padding-left: 40px;padding-right: 40px;}
  .homepage-slider-field .wrapper .main-carousel .swiper-wrapper .swiper-slide .image-field .text-container {transform: translateY(-50%);}
  .about-company-field .container .wrapper {grid-template-columns: minmax(0, 1fr);grid-gap: 24px;}
  .about-company-field .container .wrapper .image-field .image--one {position: relative;top: 0;}
  .about-company-field .container .wrapper .image-field .image--two {bottom: -10px;left: -10px;}
  .about-company-field .container .wrapper .text-field .mini-text {margin: auto;margin-bottom: 20px;}
  .about-company-field .container .wrapper .text-field .mini-text:before {left: 50%;transform: translateX(-50%);}
  .about-company-field .container .wrapper .text-field .title {margin-bottom: 30px;text-align: center;}
  .about-company-field .container .wrapper .text-field p {text-align: center;}
  .project-box {grid-template-columns: minmax(0, 3fr);}
  .project-box .image-field {display: none;}  
  .project-box .info-field {height: 300px;margin: 0 0;}
  .project-box .image-field .img {height: 300px;}
  .project-box .info-field .image {width: calc(100% - 60px);background: transparent;}
  .project-box:hover .info-field .info-content .image {background: transparent;}
  .project-box:before {background: transparent;}
  .project-box:after {background: $primary-color;}
  .project-box .info-field .text-field {background: #000;}
  .project-filter-field .container .wrapper .filter-list {margin-bottom: 20px;-webkit-mask-image: linear-gradient(90deg,#000 calc(100% - 120px),transparent);mask-image: linear-gradient(90deg,#000 calc(100% - 120px),transparent);overflow-x: auto;overflow-y: hidden;scrollbar-width: none;width: 100%;}
  .project-filter-field .container .wrapper .filter-list ul {justify-content: unset;padding-bottom: 10px;}
  .project-filter-field .container .wrapper .filter-list ul li {margin-right: 30px;white-space: nowrap;}
  .project-filter-field .container .wrapper .filter-list ul li:last-child {margin-right: 0;padding-right: 50px;}
  .blog-content-field .container .wrapper {grid-template-columns: repeat(1, minmax(0, 1fr));grid-gap: 30px;}
  .blog-content-field .container .wrapper .information-field .text-field {grid-template-columns: minmax(0, 1fr);}
  .blog-content-field .container .wrapper .information-field .text-field .right-field {display: none;}
  .blog-content-field .container .wrapper .information-field .text-field .title {grid-column: span 1;}
  .blog-content-field .container .wrapper .content-field .main-image .img {height: 300px;}
  .hr-job-field .container .wrapper .job-field ul li {padding: 30px 20px;grid-template-columns: repeat(1, minmax(0, 1fr));}
  .hr-job-field .container .wrapper .job-field ul li .title-field .title {text-align: center;}
  .hr-job-field .container .wrapper .job-field ul li .select-field {justify-self: center;}
  .map-field .wrapper .image-field .text-container .text-wrapper {grid-template-columns: minmax(0, 1fr);}
  .map-field .wrapper .image-field .text-container {position: relative;top: unset;transform: unset;}
  .map-field .wrapper {order: 1;background: #e6e6e6;padding-top: calc(var(--header-height) + 50px);padding-bottom: $page-content-padding;}
  .map-field-inner {order: 2;}
  .map-field {display: flex;flex-direction: column;max-height: unset;min-height: unset;height: 100%;}
  .map-field-inner .map-selector {transform: translate(calc(-50% + 200px), -50%);}
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container .text-wrapper,
  .project-detail-field .main-content .main-container .tab--two .wrapper .text-container .text-wrapper {grid-gap: 30px;}
  .project-box .info-field .info-content {height: 100%;}
  .project-box .info-field .text-field {bottom: 20px;transform: translateY(0);}
  .project-list-field {padding-top: 0;}
  .project-box:hover .info-field .info-content {height: 100%;}
  .project-box:hover .info-field .info-content .text-field .current {margin-bottom: 0;transform: translateY(-50px);}
  .project-box:hover .info-field .info-content .text-field .button {transform: translateY(-40px);}
  .project-detail-field .main-content .main-container .tab--four .wrapper .image-content-field .image-content {height: 100vh;}
  .project-detail-field .main-content .main-container .tab--four .wrapper .image-content-field .image-content--two .vessel-placeholder .icon-field {width: 100px;height: 100px;}
  .project-detail-field .main-content .main-container .tab--four .wrapper .image-content-field .image-content--two .vessel-placeholder .icon-field .img {max-width: 50px;}

  .homepage-three-d-field .three-d-field,
  .threed-field .obj-field,
  .homepage-three-d-field .three-d-field .wrapper .image-field,
  .threed-field,
  .threed-field .placeholder-image .main-image .img {height: 300px;}
  .homepage-three-d-field .three-d-field .wrapper .wave {height: calc(300px / 2);}
  .threed-field .obj-field .viewer {padding-bottom: 0;}
}
