@for $i from 50 through 750 {
  @if $i % 25 == 0 {
    $value: $i * 1px;
    .h--#{$i} {
      height: $value !important;
    }
  }
}

@media only screen and (max-width: 1680px) {
  @for $i from 50 through 750 {
    @if $i % 25 == 0 {
      $value: $i * 1px;
      @if $i > 550 {
        .h--#{$i} {
          height: 550px !important;
        }
      }
    }
  }
}

