.social-media-ul {
  display: flex;
  flex-direction: column;
  .sm-li {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    transition: 0.45s;
    &:last-child {
      margin-bottom: 0;
    }
    .sm-link {
      display: flex;
      transform: scale(1);
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      .icon {
        display: flex;
        transition: 0.45s;
        @include svg-color(#fff);
        opacity: 0.5;
        svg {
          max-width: 20px;
          max-height: 20px;
          width: 20px;
          height: 20px;
        }
      }
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border: 1px solid transparent;
        transform: scale(1);
        pointer-events: none;
        border-radius: 100px;
        transition: 0.45s;
      }
      &:hover {
        &:before {
          border-color: #fff;
        }
        .icon {
          transform: scale(0.8);
          opacity: 1;
        }
        @include svg-color(#fff);
      }
    }
  }
}
