@media only screen and (max-width: 1440px) {
  .homepage-about-field .shape {transform: translateY(-50%) translateX(calc(-50% - 100px));}
  .homepage-latest-field .container .wrapper .homepage-latest-carousel .swiper-wrapper .swiper-slide .latest-box {height: 600px;}
  .hr-form-field .container .background-shape {transform: translateY(-50%) translateX(50%);opacity: .15;}
  .hr-form-field .container .background-image {opacity: .15;}

  .homepage-three-d-field .three-d-field,
  .threed-field .obj-field,
  .homepage-three-d-field .three-d-field .wrapper .image-field,
  .threed-field,
  .threed-field .placeholder-image .main-image .img {height: 500px;}
  .homepage-three-d-field .three-d-field .wrapper .wave {height: calc(500px / 2);}
}
