.newsletter {
  .form {
    display: grid;
    grid-template-columns: minmax(0, 1fr) minmax(0, 1fr) minmax(0, auto);
    .form-title {
      color: #fff;
      font-size: 20px;
      font-weight: 500;
      line-height: 1.5;
    }
    .button-field {
      .button {
        height: 100%;
      }
    }
    .gdpr-text {
      color: #fff;
    }
  }
}
