@media only screen and (max-width: 1024px) {
  .homepage-project-box .image-field .img {height: 200px;}
  .blog-box .image-field .img {height: 300px;}
  .blog-content-field .container .wrapper .content-field .main-image .img {height: 400px;}
  footer .footer-container .footer-top .container .wrapper,
  footer .footer-container .footer-middle .container .wrapper {grid-template-columns: minmax(0, 1fr);grid-gap: 24px;}
  footer .footer-container .footer-top .container .wrapper .footer-logo-field {text-align: center;}
  footer .footer-container .footer-top .container .wrapper .footer-logo-field .logo {justify-content: center;}
  .homepage-slider-field .wrapper .main-carousel .swiper-wrapper .swiper-slide .image-field .text-container .text-wrapper {grid-template-columns: minmax(0, 7fr) minmax(0, 5fr);}
  .homepage-about-field .shape {display: none;}
  .homepage-about-field .container .wrapper .text-field {margin: 40px 0;}
  .page-content {padding-top: 40px;padding-bottom: 40px;}
  .homepage-testimonial-field .container .wrapper .text-field p {line-height: 1.5 ;}
  footer .footer-container {padding-top: 200px;}
  .mega-menu .mega-menu-inner {grid-template-columns: minmax(0, 1fr);}
  .mega-menu .mega-menu-inner .image-field {display: none;}
  .mega-menu {width: 100%;border-radius: unset;}
  .mega-menu .mega-menu-inner .mega-menu-field {padding: 0;display: flex;justify-content: center;}
  .mega-menu .mega-menu-inner .mega-menu-field .mm-ul .mm-li .mm-link {justify-content: center;}
  .about-mission-field .container .wrapper .image-field .image--rope {transform: translateY(-50%) translateX(-10%);}
  .about-mission-field .container .wrapper {grid-template-columns: minmax(0, 1fr);grid-gap: 24px;position: relative;}
  .about-mission-field .container .wrapper .image-field {position: absolute;top: 50%;transform: translateY(-50%);width: 100%;height: 100%;opacity: .25;pointer-events: none;}
  .about-mission-field .container .wrapper .text-field ul li {margin-bottom: 50px;}
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container .text-wrapper,
  .project-detail-field .main-content .main-container .tab--two .wrapper .text-container .text-wrapper {grid-gap: 100px;}

  .homepage-three-d-field .three-d-field,
  .threed-field .obj-field,
  .homepage-three-d-field .three-d-field .wrapper .image-field,
  .threed-field,
  .threed-field .placeholder-image .main-image .img {height: 300px;}
  .homepage-three-d-field .three-d-field .wrapper .wave {height: calc(300px / 2);}
  .threed-field .obj-field .viewer {padding-bottom: 50px;}
  .breadcrumb .wrapper .image-field .img {height: 400px;}
  .breadcrumb .wrapper .image-field .text-container {transform: translateY(calc(-50% + (var(--header-height) / 2)));}
  .project-box .image-field .img {height: 350px;}
  .project-box {grid-template-columns: minmax(0, 6fr) minmax(0, 5fr);}
  .project-box:hover .info-field .info-content .image {transform: translateY(-20px);}
  .blog-box-field .blog-box:nth-child(odd),
  .blog-box-field .blog-box:nth-child(even) {transform: translateY(0);}
  .map-field .wrapper .image-field .text-container .text-wrapper .content .team-info ul {grid-gap: 30px;}
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container .text-wrapper .right-field .carousel-controller .carousel-navigation .project-media-next .icon svg,
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container .text-wrapper .right-field .carousel-controller .carousel-navigation .project-media-prev .icon svg {width: 30px;}
  .project-detail-field .main-content .main-container .tab--three .wrapper {grid-template-rows: repeat(3, minmax(0, 1fr));}
  .project-detail-field .main-content .main-container .tab--three .wrapper .carousel-container {grid-row: span 2;}
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container {grid-row: span 1;}

  .project-detail-field .main-content .main-container .tab--four .wrapper {grid-template-rows: repeat(3, minmax(0, 1fr));}
  .project-detail-field .main-content .main-container .tab--four .wrapper .image-content-field {grid-row: span 2;}
  .project-detail-field .main-content .main-container .tab--four .wrapper .text-container {grid-row: span 1;position: relative;bottom: 0;}
  .project-detail-field .main-content .main-container .tab--four .wrapper .text-container .text-wrapper {grid-template-columns: repeat(1, minmax(0, 1fr));}
  .project-detail-field .main-content .main-container .tab--four .wrapper .text-container .text-wrapper .right-field {grid-template-columns: repeat(1, minmax(0, 1fr));}
  .project-detail-field .main-content .main-container .tab--four .wrapper .text-container .text-wrapper .right-field .button-field {grid-column-start: 1;}
  .project-detail-field .main-content .main-container .tab--four .wrapper .text-container .text-wrapper .right-field .button-field .button-info {display: none;}

  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .swiper-wrapper .swiper-slide .content .image-field .text-container .text-wrapper {grid-template-columns: repeat(1, minmax(0, 1fr));margin-bottom: 50px;}
  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .carousel-controller {bottom: 20px;}
  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .carousel-controller .button-container .button-field {justify-content: center;}
  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .swiper-wrapper .swiper-slide .content .image-field .text-container .text-wrapper .right-field .button-field {justify-content: flex-start;}
  .project-detail-field .tab-field .container {padding: 0;}
  .project-detail-field .tab-field .container .wrapper .tab-content ul {display: flex;}
  .project-detail-field .tab-field .container .wrapper .tab-content ul li {margin-right: 30px;padding: 15px;}
  .project-detail-field .tab-field .container .wrapper .tab-content ul li:last-child {margin-right: 0;padding-right: 50px;}
  .project-detail-field .tab-field .container .wrapper .tab-content ul li .text {white-space: nowrap;}
  .project-detail-field .tab-field .container .wrapper .tab-content {position: relative;overflow-x: auto;overflow-y: hidden;padding-bottom: 0;mask-image: linear-gradient(90deg, #000 calc(100% - 120px), transparent);}

  // 40 - 36
  .homepage-slider-field .wrapper .main-carousel .swiper-wrapper .swiper-slide .image-field .text-container .text-wrapper .content .title {font-size: 36px;}

  // 36 - 32
  .breadcrumb .wrapper .image-field .text-container .text-field .navigation .title,
  .mega-menu .mega-menu-inner .mega-menu-field .mm-ul .mm-li .mm-link .text,
  .homepage-project-box .text-field .name,
  .title-section .title-field .title {font-size: 32px;}

  // 32 - 28
  .map-field .wrapper .image-field .text-container .text-wrapper .content .contact-info .main-title,
  .blog-content-field .container .wrapper .information-field .text-field .title,
  .project-filter-field .container .wrapper .title-field .title,
  .about-mission-field .container .wrapper .text-field ul li .title,
  .about-company-field .container .wrapper .text-field .title,
  .project-detail-field .main-content .main-container .tab--one .wrapper .project-carousel .swiper-wrapper .swiper-slide .content .image-field .text-container .text-wrapper .left-field .title,
  .project-detail-field .main-content .main-container .tab--four .wrapper .text-container .text-wrapper .left-field .title,
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container .text-wrapper .left-field .title,
  .project-detail-field .main-content .main-container .tab--two .wrapper .text-container .text-wrapper .left-field .title
  .about-text-field .container .wrapper .text-field .title,
  .contact-form-field .container .wrapper .text-field .title,
  .homepage-about-field .container .wrapper .text-field .title {font-size: 28px;}

  // 28 - 24
  .blog-content-field .container .wrapper .information-field .text-field .left-field .date,
  .blog-box--single .image-field .text-field .left-field .name,
  .blog-box--featured .image-field .text-field .left-field .name,
  .map-field .wrapper .image-field .text-container .text-wrapper .content .contact-info .location,
  .hr-job-field .container .wrapper .job-field ul li .title-field .title,
  .homepage-three-d-field .text-field .main-text {font-size: 24px;}

  // 20 - 18
  .about-testimonials-field .container .wrapper .text-field p,
  .project-box .info-field .text-field .current .name,
  .about-text-field .container .wrapper .text-field p,
  .breadcrumb .wrapper .image-field .text-container .text-field .navigation ul li a .text,
  .about-company-field .container .wrapper .text-field p,
  .about-mission-field .container .wrapper .text-field ul li .desc,
  .project-detail-field .main-content .main-container .tab--three .wrapper .text-container .text-wrapper .left-field .category,
  .homepage-testimonial-field .container .wrapper .text-field p {font-size: 18px;}
}
