.custom-select {
  select {
    padding-right: 50px;
    appearance: none;
    &::-ms-expand {
      display: none;
    }
    &:hover {
      & ~ .custom-select::after {
        transform: rotate(180deg);
      }
    }
  }
  &::after {
    content: "";
    position: absolute;
    pointer-events: none;
    @include background-image(
      "data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAxNS4zNSA4LjY4Ij48cGF0aCBkPSJNNy42OCw4LjY4aDBBMSwxLDAsMCwxLDcsOC4zOEwuMjksMS43MUExLDEsMCwwLDEsMS43MS4yOWw2LDYsNi02YTEsMSwwLDAsMSwxLjQxLDAsMSwxLDAsMCwxLDAsMS40Mkw4LjM4LDguMzhBMSwxLDAsMCwxLDcuNjgsOC42OFoiLz48L3N2Zz4="
    );
    display: block;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    width: 12px;
    height: 12px;
    filter: invert(69%) sepia(1%) saturate(0%) hue-rotate(337deg)
      brightness(89%) contrast(86%);
  }
  option {
    color: #222;
  }
}
