/* [------------------] Default Values [------------------] */

    $font-family                    : 'Raleway';
    $font-size                      : 16;
    $line-height                    : 1.5;
    $default-space                  : 30px; 
    $default-container              : 1440px;
    $popup-max-width                : 800px;
    $image-radius                   : 30px;
    $pagination-radius              : 50px;
    $page-content-padding           : 60px;
    $homepage-testimonial-height    : 600px;
    $homepage-latest-height         : 700px;
    $homepage-three-height          : 600px;
    $product-wave-height            : 400px;

    :root {
        --header-height         : 120px;
        --page-content-padding  : 60px;
    }

/* [------------------] Form Values [------------------] */

    $input-border-radius        : 100px;
    $textarea-border-radius     : 30px;
    $checkbox-border-radius     : 100%;
    $input-height               : 60px;
    $textarea-height            : 125px;
    $input-background           : transparent;
    $input-color                : #fff;
    $input-border               : 0;
    $input-padding              : 0 30px;
    $textarea-padding           : 20px 30px;

/* [------------------] Button Values [------------------] */

    $button-height              : 44px;
    $button-padding             : 0 30px;
    $button-border-radius       : 30px;
    $button-border              : 1px solid transparent;

/* [------------------] Colors [------------------] */
                    
    $alabaster                  : #F7F7F7;
    $mercury                    : #E6E5E5;
    $cardinal                   : #CB232C;
    $clay-creek                 : #837861;

    $main-color                 : #545DE5;
    $primary-color              : #545DE5;
    $secondary-color            : $cardinal;
    $text-color                 : #fff;
    $border-color               : rgba(#fff, 0.5);
    $body-background-color      : black;
    $box-shadow                 : 0 5px 25px -5px rgba(#000, 0.1);
    $box-shadow-hover           : 0 5px 25px -5px rgba(#000, 0.25);
    $box-shadow-primary         : 0 5px 25px -5px rgba($primary-color, 0.3);
    $box-shadow-primary-hover   : 0 5px 25px 0 rgba($primary-color, 0.5);