.homepage-slider-field {
  height: 100vh;
  .wrapper {
    .main-carousel {
      .swiper-wrapper {
        .swiper-slide {
          .image-field {
            position: relative;
            .img {
              height: 100vh;
              position: relative;
              &:before {
                @include pseudo-element;
                background: #000;
                background: linear-gradient(
                  180deg,
                  rgba(#000, 0) 0%,
                  rgba(#000, 1) 100%
                );
              }
              video {
                width: 100%;
                height: 100%;
                object-fit: cover;
              }
            }
            .text-container {
              position: absolute;
              top: 50%;
              transform: translateY(calc(-50% + (var(--header-height) / 2)));
              z-index: 5;
              left: 0;
              right: 0;
              margin: auto;
              .text-wrapper {
                display: grid;
                grid-template-columns: minmax(0, 5fr) minmax(0, 7fr);
                .content {
                  opacity: 0;
                  visibility: hidden;
                  display: none;
                  &.active-text {
                    opacity: 1;
                    visibility: visible;
                    display: block;
                  }
                  .title {
                    font-size: 48px;
                    color: #fff;
                    line-height: 1.25;
                    font-weight: bold;
                    margin-bottom: 40px;
                  }
                  .description {
                    font-size: 20px;
                    color: #fff;
                    line-height: 1.5;
                    font-weight: 300;
                    margin-bottom: 60px;
                  }
                }
              }
            }
          }
        }
      }
      .carousel-controller {
        .carousel-pagination {
          position: absolute;
          margin: auto;
          left: 0;
          right: 0;
          z-index: 15;
          top: 50%;
          transform: translateY(calc(-50% + (var(--header-height) / 2)));
          pointer-events: none;
          .swiper-pagination {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            pointer-events: none;
            top: 50%;
            position: relative;
            transform: translateY(-50%);
            .swiper-pagination-bullet {
              background: #fff;
              width: 10px;
              height: 10px;
              border-radius: 50%;
              position: absolute;
              position: relative;
              margin-bottom: 40px;
              pointer-events: all;
              transition: 0.45s;
              &:hover {
                opacity: 0.8;
              }
              &:last-child {
                margin-bottom: 0;
              }
              .content {
                display: flex;
                flex-direction: column;
                opacity: 0;
                visibility: hidden;
                transition: 0.45s;
                .value {
                  font-size: 18px;
                  color: #fff;
                  position: absolute;
                  left: -350%;
                  top: 50%;
                  transform: translateY(-50%);
                }
                .icon {
                  width: 40px;
                  height: 40px;
                  left: -15px;
                  top: -15px;
                  position: absolute;
                  svg {
                    width: 40px;
                    height: 40px;
                  }
                }
              }
            }
            .swiper-pagination-bullet-active {
              .content {
                opacity: 1;
                visibility: visible;
              }
            }
            .circle-inner {
              fill: transparent;
              stroke: #ffffff;
              stroke-dasharray: 200;
              stroke-dashoffset: 200;
            }

            .circle-inner.active {
              animation: fill 15s forwards;
            }

            @keyframes fill {
              0% {
                stroke-dashoffset: 200;
              }
              100% {
                stroke-dashoffset: 0;
              }
            }
          }
        }
      }
    }
  }
}
.homepage-about-field {
  position: relative;
  background: #262525;
  &:hover {
    .container {
      .wrapper {
        .text-field {
          .mini-text {
            &:before {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .shape {
    position: absolute;
    left: 0%;
    top: 50%;
    transform: translateY(-50%) translateX(calc(-50%));
    z-index: 5;
    pointer-events: none;
  }
  .background {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
    z-index: 4;
    width: 100%;
    height: 100%;
    pointer-events: none;
    &:before {
      @include pseudo-element;
      height: 20%;
      top: unset;
      bottom: 0;
      background: linear-gradient(
        180deg,
        rgba(#262525, 0) 0%,
        rgba(#262525, 1) 100%
      );
      z-index: 5;
    }
    .img {
      height: 100%;
    }
  }
  .container {
    .wrapper {
      .text-field {
        margin-bottom: 80px;
        position: relative;
        z-index: 10;
        .mini-text {
          font-size: 16px;
          color: $primary-color;
          line-height: 1.5;
          font-weight: bold;
          position: relative;
          width: fit-content;
          margin: auto;
          padding-bottom: 10px;
          margin-bottom: 50px;
          &:before {
            content: "";
            position: absolute;
            width: 50%;
            height: 3px;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            background: $primary-color;
            transition: 0.45s ease all;
          }
        }
        .title {
          font-size: 36px;
          color: #e3e3e3;
          line-height: 1.25;
          font-weight: bold;
          margin-bottom: 30px;
        }
        .editor {
          margin-bottom: 30px;
          p,
          ul li {
            font-size: 18px;
            color: #e3e3e3;
            line-height: 1.5;
          }
        }
      }
    }
  }
}
.homepage-testimonial-field {
  background: #242424;
  &:hover {
    .container {
      .wrapper {
        .text-field {
          .seperator {
            filter: grayscale(0);
          }
        }
      }
    }
  }
  .container {
    .wrapper {
      display: grid;
      position: relative;
      z-index: 5;
      .image-field {
        position: relative;
        height: $homepage-testimonial-height;
        .image {
          position: absolute;
          &--one {
            width: 90%;
            right: 0;
            top: 0;
            .img {
              height: $homepage-testimonial-height;
              border-radius: $image-radius;
            }
          }
          &--two {
            left: 0;
            /* bottom: -$page-content-padding * 2; */
            top: 0;
            width: 100%;
            max-width: 475px;
            z-index: 20;
            .img {
              @include svg-color(#262525);
              svg {
                height: 350px;
                width: 100%;
                path {
                  stroke-width: 25px;
                  fill: none;
                  stroke-miterlimit: 10;
                }
                image {
                  width: 100%;
                  height: 100%;
                }
              }
            }
          }
        }
      }
      .text-field {
        display: flex;
        margin-top: 30px;
        .seperator {
          display: flex;
          align-items: center;
          filter: grayscale(1);
          transition: 0.45s ease all;
          &:last-child {
            transform: rotate(180deg);
          }
        }
        p {
          font-size: 22px;
          color: #e3e3e3;
          line-height: 2;
          text-align: center;
          padding-left: 70px;
          padding-right: 70px;
        }
      }
    }
  }
}
.homepage-project-field {
  background: #242424;
  position: relative;
  overflow: hidden;
  .background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: minmax(0, 1fr);
      grid-gap: 30px;
      .homepage-project-carousel {
        width: 100%;
        overflow: visible;
        .swiper-wrapper {
          .swiper-slide {
            opacity: 0;
            visibility: hidden;
            transition: 0.45s ease all;
            &-active {
              transform: scale(1);
              opacity: 1;
              visibility: visible;
            }
            &-prev {
              transform: scale(0.8);
              opacity: 1;
              visibility: visible;
            }
            &-next {
              transform: scale(0.8);
              opacity: 1;
              visibility: visible;
            }
          }
        }
      }
    }
  }
}
.homepage-latest-field {
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: minmax(0, 1fr);
      grid-gap: 30px;
      .homepage-latest-carousel {
        width: 100%;
        position: relative;
        .background-image {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          border-radius: $image-radius;
          overflow: hidden;
          .img {
            height: $homepage-latest-height;
            opacity: 1;
            transform: scale(1);
            transition: 0.45s;
            &.active {
              opacity: 0;
              transform: scale(1.2);
            }
          }
        }
        .swiper-wrapper {
          .swiper-slide {
            position: relative;
            &:before {
              @include pseudo-element;
              width: 1px;
              height: 10%;
              top: calc(100% - 20%);
              background: rgba(#fff, 0.15);
              z-index: 1;
              pointer-events: none;
            }
            .latest-box {
              height: $homepage-latest-height;
              display: flex;
              flex-direction: column;
              justify-content: flex-end;
              align-items: center;
              &:hover {
                &:after {
                  opacity: 1;
                  visibility: visible;
                }
                .text-field {
                  transform: translateY(-70px);
                }
                .button-field {
                  transform: translateY(0px);
                  opacity: 1;
                  visibility: visible;
                }
              }
              &:before {
                @include pseudo-element;
                background: linear-gradient(
                  180deg,
                  rgba(#111111, 0) 0%,
                  rgba(#111111, 1) 100%
                );
                pointer-events: none;
              }
              &:after {
                @include pseudo-element;
                background: linear-gradient(
                  180deg,
                  rgba($primary-color, 0) 0%,
                  rgba($primary-color, 1) 100%
                );
                transition: 0.45s ease all;
                opacity: 0;
                visibility: hidden;
                pointer-events: none;
              }
              &:hover {
                .text-field {
                  opacity: 1;
                }
              }
              .text-field {
                opacity: 0.5;
                position: relative;
                left: 0;
                width: calc(100% - 30px);
                z-index: 10;
                transition: 0.45s ease all;
                .category {
                  font-size: 16px;
                  color: #fff;
                  line-height: 1.5;
                  margin-bottom: 35px;
                  text-align: center;
                  transition: 0.45s ease all;
                  &:hover {
                    color: darken(#fff, 20%);
                  }
                }
                .name {
                  font-size: 18px;
                  color: #fff;
                  line-height: 1.5;
                  text-align: center;
                  transition: 0.45s ease all;
                  &:hover {
                    color: darken(#fff, 20%);
                  }
                }
              }
              .button-field {
                position: relative;
                opacity: 0;
                visibility: hidden;
                transform: translateY(30px);
                z-index: 10;
                transition: 0.45s ease all;
                p {
                  position: relative;
                  padding-bottom: 20px;
                  .text {
                    font-size: 18px;
                    color: #fff;
                    line-height: 1.5;
                  }
                  &:before {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    width: 50%;
                    transform: translateX(-50%);
                    height: 2px;
                    background: #fff;
                    transition: 0.45s ease all;
                  }
                  &:hover {
                    .text {
                      color: darken(#fff, 20%);
                    }
                    &:before {
                      width: 100%;
                      background: darken(#fff, 20%);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
.homepage-three-d-field {
  position: relative;
  padding-bottom: 0 !important;
  &:before {
    @include pseudo-element;
    background: linear-gradient(
      -180deg,
      rgba(#000000, 0) 0%,
      rgba(#195a78, 1) 100%
    );
  }
  .three-d-field {
    height: $homepage-three-height;
    position: relative;
    .wrapper {
      .wave {
        height: calc(($homepage-three-height / 2));
        position: absolute;
        width: 100%;
        bottom: 0;
        pointer-events: none;
        &:before {
          @include pseudo-element;
          background: linear-gradient(
            180deg,
            rgba(#000, 0) 0%,
            rgba(#000, 1) 80%
          );
          z-index: 15;
        }
        .img {
          position: absolute;
          bottom: 0;
          left: 0;
          height: 100%;
          width: 100%;
          background-repeat: no-repeat !important;
          background-size: cover !important;
        }
        &--back {
          z-index: 5;
        }
        &--front {
          z-index: 15;
        }
      }
      .image-field {
        position: absolute;
        height: $homepage-three-height;
        z-index: 10;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
  .text-field {
    margin-bottom: 50px;
    .image {
      img {
        max-width: 200px;
      }
      margin-bottom: 30px;
    }
    .main-text {
      color: #fff;
      font-size: 30px;
      font-weight: 600;
      line-height: 1.5;
      margin-bottom: 30px;
    }
  }
}
.homepage-news-field {
  overflow: hidden;
  .container {
    .wrapper {
      display: grid;
      grid-template-columns: minmax(0, 1fr);
      grid-gap: 30px;
      .homepage-news-carousel {
        width: 100%;
        overflow: visible;
        position: relative;
        &:before {
          @include pseudo-element;
          left: -100%;
          background: #000;
          z-index: 13;
          pointer-events: none;
        }
        .swiper-wrapper {
          .swiper-slide {
            &-prev {
              pointer-events: none;
            }
            .blog-box--single .image-field .text-field {
              grid-template-columns: minmax(0, 1fr);
            }
            .blog-box--single .image-field .text-field .right-field {
              writing-mode: unset;
              text-orientation: unset;
              transform: translateY(20px) rotate(0);
            }
            .blog-box--single .image-field .text-field .right-field .button {
              padding: 0 30px;
              height: $button-height;
            }
            .blog-box--single:hover .image-field .text-field .right-field {
              transform: translateY(0) rotate(0);
            }
          }
        }

        .total-count-field {
          font-size: 18px;
          color: #fff;
          line-height: 1.5;
          font-weight: 500;
          display: flex;
          .current {
          }
          .seperator {
            display: flex;
            position: relative;
            width: 20px;
            margin: 0 10px;
            &:before {
              content: "";
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              width: 20px;
              height: 1px;
              background: #fff;
            }
          }
        }
      }
    }
  }
}
