.table-wrapper {
  position: relative;
  z-index: 1;
  overflow-x: auto;
  overflow-y: hidden;
  &.w-768 {
    table {
      min-width: 768px;
    }
  }
  table {
    width: 100%;
    border: 0;
    line-height: 1.25;
    tr {
      color: #fff;
      vertical-align: middle;
      transition: 0.3s ease all;
      &:not(:last-child) {
        border-bottom: 1px solid $border-color;
      }
      &:hover {
        background-color: rgba(black, 0.025);
      }
      &:last-child {
        td {
          border-bottom: 1px solid $border-color;
        }
      }
    }
    td {
      font-size: 18px;
      vertical-align: middle;
      transition: 0.3s ease all;
      &:first-child {
        font-weight: bold;
      }
      &:hover {
        background-color: rgba(black, 0.025);
      }
    }
    th {
      vertical-align: middle;
      background-color: #eaeaea;
      color: #fff;
      text-align: left;
      &:first-child {
        border-top-left-radius: 20px;
      }
      &:last-child {
        border-top-right-radius: 20px;
      }
    }
    td,
    th {
      padding: 15px 0;
    }
  }
}
